.error {
  position: relative;

  &__bg {
    position: absolute;
    z-index: -1;
    right: 4rem;
    top: 4rem;
    width: 40.5rem;
    height: auto;
  }
  &__bg-flowers {
    position: absolute;
    z-index: -1;
    left: -4.8rem;
    top: -17.6rem;
    width: 37.2975rem;
    height: auto;
    }

  &__wrap {
    max-width: 39.0625rem;
  }

  &__title {
    text-align: left;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 2rem;
    line-height: 120%;
    color: var(--2-4);
    margin-bottom: 2.5rem;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-bottom: 1.875rem;
    span {
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 1.5rem;
      line-height: 120%;
      color: var(--2-4);
    }
    &:nth-child(4) {
      margin-bottom: 1.25rem;
    }
  }

  &__list {
    margin-left: 1.925rem;
    margin-bottom: 2.5rem;
  }

  &__list-item {
    list-style: disc;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
  }

  &__text-offer {
    text-align: center;
    margin-bottom: 3.75rem;
  }

  &__btn {
    width: 18rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 3.75rem;
  }

  &__wrap-contact {
    position: relative;
    text-align: right;
  }

  &__wrap-contact--text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
    &:not(:last-child){
      margin-bottom: 1.25rem;
    }
  }

  &__wrap-contact--link {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 120%;
    color: var(--2-4);
  }
}

@media (max-width: 768px) {
  .error__wrap {
    width: 100%;
  }
  .error__title{
    font-size: 1.5rem;
    margin-bottom: 1.125rem;
    }
  .error__text{
    font-size: 1rem;
    margin-bottom: 0.875rem;
    max-width: 60%;
  }
  .error__list{
    margin-bottom: 2.5rem;
    margin-left: 1.3925rem;
    max-width: 55%;
  }
  .error__list-item {
    font-size: 0.875rem;
  }
  .error__bg {
    width: 25.25rem;
    right: 1rem;
  }
  .error__text:nth-child(4) {
    margin-bottom: 0.5625rem;
}
.error__text-offer {
  font-size: 1rem;
  width: 100%;
  max-width: none;
  margin-bottom: 2.5rem;
}
.error__btn {
  font-size: 1.25rem;
  margin-bottom: 2.475rem;
}
.error__wrap-contact {
  max-width: 26rem;
  margin-left: auto;
}
.error__wrap-contact--text {
  font-size: 1rem;
  width: 100%;
  text-align: left;
}
.error__wrap-contact--link {
  font-size: 1.25rem;
}
.error__wrap-contact--text:not(:last-child) {
  margin-bottom: 0.525rem;
}
.error__bg-flowers {
  width: 18.6875rem;
  height: auto;
  left: -21.8rem;
    top: -10.6rem;
}
}

@media (max-width: 320px) {
  .error__title{
    text-align: center;
    margin-bottom: 1.4125rem;

  }
  .error__text {
    max-width: 100%;
    width: 100%;
  }
  .error__text {
    font-size: 0.875rem;
  }
  .error__text span  {
    font-size: 1.125rem;
  }
  .error__list {
    margin-bottom: 2.5rem;
    margin-left: 1.3925rem;
    max-width: 100%;
}
.error__list-item {
  font-size: 0.75rem;
}
.error__text-offer {
  text-align: left;
  margin-bottom: 1.2rem;
}
.error__btn {
  font-size: 1rem;
}
.error__wrap-contact--text {
  text-align: right;
}
.error__text:nth-child(4) {
  margin-bottom: 0.35625rem;
}
.error__bg {
  width: 18.75rem;
  top: 20rem;
  right: 0;
}
.error__wrap {
  padding-bottom: 10.5rem;
}
.error__wrap-contact--text{
  font-size: 0.875rem;
}

.error__wrap-contact--link{
  font-size: 1.125rem;
}
.error__btn {
  margin-bottom: 1.90475rem;
}
}

