.raz {
  --borderW: 0.625rem;
  position: relative;
  display: inline-grid;
  place-content: center;
  width: 10rem;
  height: 10rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 0.625rem solid rgba(165, 205, 185, 0.1);
}

.raz::before {
  content: "";
  position: absolute;
  inset: calc(-1 * var(--borderW));
  /* border: var(--borderW) dotted darkturquoise; */
  border: 0.625rem solid var(--2-1);
  border-radius: 50%;
  animation: progress 3s cubic-bezier(0.25, 0.4, 0.75, 0.6) forwards;
}

@keyframes progress {
  0% {
    clip-path: polygon(50% 50%, 50% -21%, 50% -21%, 50% -21%, 50% -21%, 50% -21%);
  }
  25% {
    clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 121% 50%, 121% 50%, 121% 50%);
  }
  50% {
    clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, 50% 121%, 50% 121%);
  }
  75% {
    clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, -21% 50%, -21% 50%);
  }
  100% {
    clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, -21% 50%, 50% -21%);
  }
}
