.practice {
  position: relative;

  &__title {
  }

  &__content {
    position: relative;
    max-width: 66.25rem;
    margin: auto;
    margin-bottom: 2.575625rem;
  }

  &__bg {
    position: absolute;
    z-index: -1;
    width: 28.4375rem;
    height: auto;
    right: -9rem;
    top: -12rem;
  }

  &__content-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
    &:not(:last-child) {
      margin-bottom: 1.25rem;
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.875rem;
    max-width: 66.25rem;
    margin: auto;
    margin-bottom: -0.7rem;
  }

  &__img {
    flex: 0 0 36.5625rem;
    height: auto;
  }

  &__descr {
    border-radius: 0 0.625rem 0.625rem 0;
    flex: 0 0 27.875rem;
    height: auto;
    background: #edf5f1;
    padding: 0.9375rem 0.625rem;
    border-left: 0.125rem solid var(--2-1);
  }

  &__descr-title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-bottom: 0.5rem;
  }

  &__descr-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
  }
}


.practice {

  &__list {
    position: relative;
    max-width: 66.25rem;
    margin: auto;
  }

  &__list-bg {
    position: absolute;
    z-index: -1;
    width: 31.3125rem;
    height: auto;
    right: -12rem;
    top: -32rem;

  }
  &__bg-big{
    position: absolute;
    z-index: -1;
    width: 64.5rem;
    height: auto;
    left: 0rem;
    bottom: -2rem;
  }
  &__list-item {
    display: flex;
    align-items: start;
    &:not(:last-child){
      margin-bottom: 2.5rem;
    }
  }

  &__list-num {
    display: flex;
    align-items: start;
    flex-shrink: 0;
    flex-grow: 1;
    span {
      display: inline-block;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 2rem;
      line-height: 120%;
      color: var(--2-3);
    }
  }

  &__list-img {
    width: 0.5625rem;
    height: 0.5625rem;
    margin-top: 1rem;
  }

  &__list-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-left: 0.625rem;
  }
}

@media (max-width: 768px) {
.practice__content-text {
  font-size: 0.875rem;
}
.practice__content-text:not(:last-child) {
  margin-bottom: 0.3025rem;
}
.practice__list-text {
  font-size: 0.875rem;
}
.practice__list-num {
  align-items: baseline;
}
.practice__list-num span{
  font-size: 1.5rem;
}
.practice__list {
  margin-top: 1.8rem;
}

.practice__list-img {
  width: 0.375rem;
  height: 0.375rem;
  margin-top: 0rem;
  flex-shrink: 0;
}
.practice__content {
  margin-bottom: 0.80975625rem;
}
.practice__list-item:not(:last-child) {
  margin-bottom: 1.1995rem;
}
.practice__list-bg {
  width: 37.8125rem;
  height: auto;
}
.practice__list-bg {
  display: none;
}
.practice__img {
  flex: 0 0 26rem;
height: auto;
}
.practice__wrap {
  width: 100%;
  max-width: auto;
  gap: 0.625rem;
  align-items: start;
  margin-bottom: -0.2rem;
}
.practice__descr-title {
  font-size: 1.25rem;
  margin-bottom: 0.375rem;
}
.practice__descr-text {
  font-size: 0.875rem;
}
.practice__descr {
  flex: auto;
  padding: 0.59375rem 0.1rem 0.403625rem 0.59375rem;
  margin-top: 0.45rem;
}


}

@media (max-width: 550px) {
  .practice__title {
    line-height: 98%;
  }
  .practice__content-text {
    font-size: 0.75rem;
  }
  .practice__content-text:not(:last-child) {
    margin-bottom: 0.73025rem;
}
.practice__list-text {
  font-size: 0.85688rem;
}
.practice__list {
  margin-top: 1.08rem;
}
.practice__list-item:not(:last-child) {
  margin-bottom: 0.41995rem;
}
.practice-main__wrap {
  display: block;
}
.practice-main__text {
  font-size: 0.75rem;
  line-height: 157%;
}
.practice-main__title {
  font-size: 0.875rem;
}
.practice-main__descr {
  margin-left: 1.2rem;
  margin-top: 0.75rem;
  padding: 0.59375rem 0.1rem 0.6403625rem 0.59375rem;
}
.practice__bg {
  width: 12.6875rem;
  right: 0rem;
    top: 40rem;
}
}

