.my-services {
    padding-bottom: 7rem;
		&__wrap {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 4.875rem 1.8125rem;
      margin-top: 6.75rem;
		}

		&__item {
      position: relative;
      border-radius: 0.625rem;
      flex: 0 0 31.8%;
      height: 18.4375rem;
      box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
      background: rgba(165, 205, 185, 0.1);
      padding: 4.375rem 1.25rem 1.25rem 1.25rem;
      text-align: center;
		}

		&__item-img {
      overflow: hidden;
      position: absolute;
      top: -3.125rem;
      left: 50%;
      transform: translateX(-50%);
      width: 6.825rem;
      height: 6.825rem;
      border-radius: 50%;
		}

		&__item-title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.5rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-4);
      margin-bottom: 2.0625rem;
		}

		&__item-text {
      font-family: var(--font-family);
      font-weight: 300;
      font-size: 1.25rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-dark);
		}
    &__item:hover {
      box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
    }
    &__item:active {
      box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.1);
    }
}

.my-services__item {
  &:hover {
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
    .my-services__item-title {
      transform: scale(1.02);
      transition: all 0.1s;
    }
    .my-services__item-text {
      transform: scale(1.02);
      transition: all 0.1s;
    }
    .my-services__item-img{
      overflow: hidden;
      img {
        transition: all 0.5s;
        transform: scale(1.05);
      }
    }
  }
  &:active {
    box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.1);

    .my-services__item-title {
      transition: all 0.1s;
      transform: scale(1);
    }
    .my-services__item-text {
      transition: all 0.1s;
      transform: scale(1);
    }
    .my-services__item-img{
      img {
        transition: all 0.5s;
        transform: scale(1);
      }
    }
  }
}


@media (max-width: 768px) {
  .my-services__wrap {
    margin-top: 4.9975rem;
    gap: 3.875rem 1.18125rem;
}
.my-services__item {
  flex: 0 0 48.598%;
  height: 11.4375rem;
  padding: 3.2375rem 1.25rem 1.25rem 1.25rem;
}
.my-services__item-img {
  width: 5.875rem;
height: 5.875rem;
  top: -3.32125rem;
}
.my-services__item-title {
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
}
.my-services__item-text {
  font-size: 0.875rem;
}
.my-services {
  padding-bottom: 3.8rem;
}
}

@media (max-width: 550px) {
  .my-services__item {
    flex: 0 0 100%;
    height: 11.4375rem;
    padding: 3.2375rem 1.25rem 1.25rem 1.25rem;
  }

  .my-services__wrap {
    margin-top: 3.73769975rem;
    gap: 3.375rem;
}
.my-services__item-img {
  width: 3.125rem;
  height: 3.125rem;
  top: -1.5rem;
}
.my-services__item {
  height: 10.84375rem;
}
.my-services__item-title {
  font-size: 1.25rem;
  margin-bottom: 0.225rem;
}
.my-services {
  padding-bottom: 2.38rem;
}
}
