.form {

  &__container {
    position: relative;
  }
  &__text {
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    margin-bottom: 3.95rem;
    }

  &__bg {
    position: absolute;
    z-index: -5;
    &--left {
      top: -6.29rem;
      left: -0.5rem;
      width: 31.625rem;
      height: auto;
    }
    &--right {
      top: 0.5rem;
       right: -1rem;
      width: 34.6875rem;
      height: auto;
    }
    &--center {
      bottom: 0rem;
      left: 50%;
      transform: translateX(-50%);
      width: 41.875rem;
      height: auto;
    }
  }

  &__wrap {
    width:32.5rem;
    margin: 0 auto;
  }

  &__content{
  }

  &__item-label {
    display: block;
    font-size: 1.25rem;
    line-height: 1.625rem;
    text-align: left;
    margin-bottom: 1.5rem;
  }

  &__item-input {
    display: block;
    width: 100%;

    padding: 0 1.25rem;
    height: 3.25rem;
    outline:none;
    border: 1px solid #989AA7;
    border-radius: 0.625rem;
    font-family: var(--font-family);
    font-size: 1.25rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.625rem;
    color: #989AA7;
  }

  &__textarea {
    padding: 0.93125rem 1.25rem;
    width: 100%;
    height: 9.25rem;
    border-radius: 0.625rem;
    font-family: var(--font-family);
    font-size: 1.25rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.625rem;
    color: var(--2-3);
    resize: none;
    outline: none;
    margin-bottom: 1.25rem;
    border: 1px solid #989AA7;
  }

  &__item-btn {
    width: 100%;
    margin-top: 2.18115rem;
  }
  &__item-label--textarea {
    margin-bottom: 0;
  }
}
input::-moz-placeholder { color: rgba(152, 154, 167, 0.5) }
input::-webkit-input-placeholder { color: rgba(152, 154, 167, 0.5) }
textarea::-webkit-input-placeholder { color: rgba(152, 154, 167, 0.5) }
textarea::-moz-placeholder { color: rgba(152, 154, 167, 0.5) }

input.just-validate-error-field {
  border: 1px solid #FF0000;
}
.connect {

  &__label-checkbox {
    display: flex;
    align-items: flex-start;
    font-weight: 300;
  }

  &__checkbox {
      appearance: none;
  }
  &__checkbox-link {
      text-decoration: underline;
  }
}
.connect__label-checkbox span{
  flex-shrink: 0;
  margin-right: 0.625rem;
  display: inline-block;
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.3125rem;
  border: 1px solid #989AA7;

}
.connect__label-checkbox input:checked + span{
  background: url('../img/union.svg') center no-repeat;
  // background-size: cover;
  background-size: auto;
}

@media (max-width: 768px) {
.form__text {
  font-size: 1.25rem;
  margin-bottom: 2.8095rem;
}
.form__title {
  margin-bottom: 0.9313rem;
}
.form__wrap {
  width: 22.995rem;
}
.form__item-label {
  font-size: 0.875rem;
line-height: 130%;
margin-bottom: 0.875rem;
}
.form__item-input {
  margin-top: 0.2rem;
}
.form__item-input {
  padding: 0 0.7025rem;
  font-size: 0.9125rem;
}
.form__textarea {
  height: 7.725rem;
  font-size: 0.825rem;
  line-height: 140%;
}
.form__item-label:last-child {
  margin-bottom: 1rem;
}
.connect__label-checkbox {
  align-items: center;
  margin-top: -1.8rem;
}
.form__item-btn {
  width: 100%;
  margin-top: 2.518115rem;
}
.form__bg--left {
  top: 3.71rem;
  left: -2.5rem;
  width: 20.625rem;
  height: auto;
}
.form__bg--right {
  top: 12.5rem;
  right: 0rem;
  width: 19.6875rem;
  height: auto;
}
.form__bg--center {
  width: 18.875rem;
  height: auto;
}
.consultation{
  padding-bottom: 2.5rem;
}
}

@media (max-width: 550px) {
  .form__title {
    line-height: 104%;
    margin-bottom: 10px;
  }
  .form__text {
    font-size: 16px;
    margin-bottom: 1.9908095rem;
  }
  .form__wrap {
    width: 100%;
}
.form__item-input {
  height: 2.525rem;
}
.form__textarea {
  height: 6.1725rem;
}
.connect__label-checkbox {
  font-size: 12px;
}
.form__item-btn {
  margin-top: 1.8518115rem;
}
.form__bg--left {
width: 134px;
top: -5.29rem;
    left: -0.5rem;
}
.form__bg--center {
  width: 310px;
}
.form__bg--right {
  display: none;
}
}
