.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 1000;

  &-call {
    display: none;
  }

  &__form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 0.625rem;
    width: 28.25rem;
    min-height: 34.875rem;
    background: var(--2-2);
    padding: 2.5rem;
  }

  &__wrap {
    position: relative;
  }
  &__close {
    position: absolute;
    width: 1.125rem;
    height: 1.125rem;
    top: -1.25rem;
    right: 0;
    cursor: pointer;
  }

  &__container {
    position: relative;
  }
  &__title {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 2rem;
    line-height: 120%;
    text-align: center;
    color: var(--2-4);
    margin-bottom: 1rem;
    text-align: center;
  }
  &__subtitle {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    text-align: center;
    color: var(--2-dark);
    margin-bottom: 2.25rem;
    text-align: center;
  }
  &__text {
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    margin-bottom: 3.5rem;
  }

  &__item-label {
    display: block;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.875rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-bottom: 1.25rem;
  }

  &__item-input {
    display: block;
    width: 100%;
    margin-top: 0.25rem;
    padding: 0 1.25rem;
    height: 3.25rem;
    outline: none;
    border: 1px solid var(--2-2);
    border-radius: 0.625rem;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 1rem;
    line-height: 130%;
    color: var(--2-1);
  }

  &__item-btn {
    width: 100%;
    margin: 2.25rem 0 2rem;
  }
}

.modal-call.active {
  display: block;
}
.modal {
  &__label p{
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.875rem;
    line-height: 130%;
    color: var(--2-dark);
  }

  &__label-checkbox {
    display: flex;
    align-items: center;
    font-weight: 300;
  }

  &__checkbox {
      appearance: none;
  }
  &__checkbox-link {
      text-decoration: underline;
  }
  &__label-checkbox span{
    flex-shrink: 0;
    margin-right: 0.625rem;
    display: inline-block;
    display: flex;
    width: 1.5rem;
    height: 1.5rem;
    border: 1px solid var(--2-3);
    border-radius: 0.3125rem;
  }
  &__label-checkbox input:checked + span{
    background: url('../img/modal-arrow.svg') center no-repeat;
    // background-size: cover;
    background-size: auto;
  }
}


.modal-social {

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 17.75rem;
    margin: 0 auto;
    gap: 0.75rem;
  }

  &__link {
    width: 2.25rem;
    height: 2.25rem;
  }
}

.modal__thanks {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 0.625rem;
  width: 28.25rem;
  height: 13.75rem;
  background: var(--2-2);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 130%;
  text-align: center;
  color: var(--2-4);
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);
  z-index: 1000;

  &-close{
    top: 1.25rem;
    right: 1.25rem;
  }
}
.modal__thanks.active{
  display: flex;
}

.up {
  &__wrap {
    position: fixed;
    display: none;
    left: 1.25rem;
    bottom: 3.125rem;
    align-items: center;
    gap: 0.625rem;
    z-index: 99;
    &.active {
      display: flex;
      position: fixed;
    }
  }



  &__btn-circle {
    width: 3.5rem;
    height: 3.5rem;
    cursor: pointer;
  }

  &__btn-text {
    width: 5.0625rem;
    height: 2rem;
  }
}
.header__nav-mobile--wrapper.active {
  z-index: 100;
}
.social {
  &__up-wrap {
    display: none;
    transition: all 0.5s;
  }

  &__up {
    position: fixed;
    bottom: 7.5125rem;
    right: 1.25rem;
    z-index: 99;
  }

  &__up-link {
    position: absolute;
    bottom: 0rem;
    left: 0rem;
    width: 3rem;
    height: 3rem;
    transition: all 0.5s;
    z-index: -1;
  }
  &__btn-open {
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
  }
  &__up-btn {
    position: relative;
  }
  &__btn-close {
    position: relative;
  }
  &__up-icon {
    width: 3.5rem;
    height: 3.5rem;
    cursor: pointer;
    flex-shrink: 0;
  }
  &__up-text {
    position: absolute;
    left: -8.59375rem;
    flex-shrink: 0;
    width: 7.9375rem;
    height: auto;
    top: 50%;
    transform: translateY(-50%);
  }

}

.online {
  display: relative;
  &__wripper {
    position: fixed;
    right: 1.25rem;
    bottom: 3.125rem;
    display: flex;
    align-items: center;
    gap: 0.625rem;
    z-index: 99;
  }
  &__up-text {
    position: absolute;
    left: -8.59375rem;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    width: 7.9375rem;
    height: auto;
  }
  &__up-icon {
    cursor: pointer;
    width: 3.5rem;
    height: 3.5rem;
    cursor: pointer;
    flex-shrink: 0;
  }
}

.social__up.active .social__up-link {
  position: relative;
  z-index: 10;
  cursor: pointer;
}
.social__up.active .social__up-wrap {
  height: 24.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.5s;
}

@media (max-width: 550px)  {
  .modal__thanks-text {
    font-size: 1.25rem;
  }
  .modal__thanks {
    width: 20rem;
    height: 10.75rem;
}
.modal__form {
  width: 20rem;
  min-height: 30.9375rem;
  padding: 2.5rem 1.25rem;
}
.modal__title  {
  font-size: 1.5rem;
  margin-bottom: 0.75rem;
}
.modal__subtitle {
  font-size: 1rem;
  margin-bottom: 1.25rem;
}
.modal__item-label {
  margin-bottom: 0.75rem;
}
.modal__item-input {
  height: 2.5rem;
  font-size: 0.75rem;

}
.modal__label p  {
  font-size: 0.75rem;
}
.modal__send {
  margin: 1.875rem 0;
  font-size: 1rem;
}
.social__up-link {
  width: 1.875rem;
  height: 1.875rem;
}
.social__up.active .social__up-wrap {
  height: 13.91875rem;
  gap: 0.2rem;
}
.up__btn-circle {
  width: 2.25rem;
  height: 2.25rem;
  cursor: pointer;
}
.up__btn-text {
  width: 3.25rem;
  height: 1.25rem;
}
.social__btn-open {
  width: 2.25rem;
  height: 2.25rem;
}

.online__up-icon {
  width: 2.25rem;
  height: 2.25rem;
}
.social__up {
  bottom: 6.03125rem;
}
.social__up-text, .online__up-text{
  height: 1.25rem;
  width: 5.0844rem;
  left: -5.459375rem;
}
}




