.services {

  &__container {
    position: relative;
  }

  &__bg {
    position: absolute;
    z-index: -1;
    top: -8rem;
    left: 3.4rem;
    width: 24.1875rem;
    height: auto;
  }

  &__title {
  }
}

@media (max-width: 768px) {
  .services__bg   {
    width: 16.875rem;
    height: auto;
    top: -5rem;
    left: -2.6rem;
  }

}
@media (max-width: 320px)  {
  .services__title {
    line-height: 100%;
    margin-bottom: 0.8rem;
  }
  .services__bg {
    width: 10.625rem;
    top: -2.5rem;
    left: -1.6rem;
  }
}

