.breadcrumbs {
  position: absolute;
  top: -4.9rem;
  left: 0rem;
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 1rem;
  line-height: 130%;
  color: var(--2-3);
  &__separator {
    font-weight: 400;
  }
  &__link-last{
    font-weight: 500;
  }
}
.about__container .breadcrumbs {
  top: 1.4rem;
}
.breadcrumbs__container .breadcrumbs {
  top: 1.4rem;
}

@media (max-width: 768px) {
  .breadcrumbs {
    top: -3rem;
    font-size: 0.75rem;
  }
  .about__container .breadcrumbs {
    top: 0.8rem;
  }
  .breadcrumbs__container .breadcrumbs {
    top: 0.8rem;
  }
}

@media (max-width: 550px){
  .breadcrumbs {
    font-size: 0.625rem;
    top: -1.2rem;
  }
  .about__container .breadcrumbs {
    top: 1rem;
  }
  .breadcrumbs__container .breadcrumbs {
    top: -1.5rem;
  }
}
