.main {

  &__about-container {
    // padding-right: max(80px, (100% - var(--content-width)) / 2);
    // margin-left: -2rem;
    // margin-left: -80px;
    padding-left: 0;
  }

  &__about-wrap {
    display: flex;
    justify-content: space-between;
  }

  &__about-bg {
    position: absolute;
    width: 23.625rem;
    height: auto;
    bottom: 2rem;
    right: -5rem;
    z-index: -1;
  }

  &__about-img {
    flex: 0 0 38.995rem;
    height: auto;
    position: relative;
    z-index: -1;
    img {
      object-position: right center;
    }
  }

  &__about-content {
    position: relative;
    margin-top: 1.325rem;
    margin-left: -1.82rem;
  }

  &__about-quote {
    font-family: "Bitter", serif;
    font-style: italic;
    font-size: 1.225rem;
    line-height: 140%;
    text-align: right;
    color: var(--2-dark);
  }

  &__about-author {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.875rem;
    line-height: 130%;
    text-align: right;
    color: var(--2-dark);
    margin-bottom: 1.5rem;
  }


  &__about-name {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 4.125rem;
    line-height: 110%;
    color: var(--2-4);
    margin-bottom: 1.875rem;
  }

  &__about-text {
    font-family: var(--font-family);
    font-size: 1rem;
    margin-bottom: 2.125rem;
    max-width: 45rem;
    line-height: 137%;
  }

  &__about-list {
    margin-bottom: 4.675rem;
    margin-left: 0.003rem;
  }

  &__about-item {
    &:not(:last-child) {
      font-weight: 300;
      margin-bottom: 0.3987188rem;
    }
  }
  &__about-item {
    display: flex;
    align-items: center;
    gap: 0.875rem;
  }
  &__about-item--img {
    flex-shrink: 0;
    width: 1.5rem;
    height: 1.5rem;
  }
  &__about-btns {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }
  &__about-btn--mobile {
    display: none;
  }
  &__about-wrap--mobile {
    display: none;
  }
  &__about-btns--mobile {
    display: none;
  }
}
.btn-consult {
  width: 18rem;
}
.btn-telegram {
  width: 18rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: 1px solid var(--2-4);
  color: var(--2-dark);
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);

  &:hover {
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);
    background: var(--2-light);
  }
  &:active {
    box-shadow: inset 0 2px 8px 0 rgba(0, 0, 0, 0.25);
    background: var(--2-light);
  }

  &:before {
    content: " ";
    background: url("../img/Telegram.svg") no-repeat;
    background-position: center;
    background-size: contain;
    width: 1.5rem;
    height: 1.5rem;
    display: inline-block;
    margin-right: 0.625rem;
  }

}

@media (max-width: 768px) {
  .main__about-img {
    flex: 0 0 23.995rem;
    height: 32rem;
  }
  .main__about-quote {
    // font-weight: 400;
    font-size: 0.851125rem;
    line-height: 130%;
    text-align: right;
    color: var(--2-dark);
  }
  .main__about-content {
    position: relative;
    max-width: 24.375rem;
    margin-top: 0.5325rem;
    margin-left: -2.82rem;
  }
  .main__about-quote--wrap {
    position: absolute;
    top: 0rem;
    left: -4.2rem;
  }

  .main__about-name {
    font-size: 3.25rem;
    margin-bottom: 1.25rem;
    line-height: 100%;
  }
  .main__about-author {
    margin-bottom: 0rem;
}
.main__about-text {
  font-size: 0.875rem;
  margin-bottom: 1.25rem;
}
.main__about-item:not(:last-child) {
  font-weight: 300;
  margin-bottom: 0.25rem;
}
.main__about-btns--desctop {
  display: none;
}
.main__about-btn--mobile {
  display: flex;
  justify-content: center;
  gap: 1.875rem;
  margin-left: 1rem;
}
.main__about-btn {

  width: 18rem;
}
.main__about-list {
  margin-bottom: 2.4675rem;
}
.main__about-author {
  font-size: 0.75rem;
}
.main__about-descr {
  padding-top: 4rem;
}
.main__about {
  margin-bottom: 5.375rem;
}
.main__about-btns--mobile {
  display: flex;
  justify-content: center;
}
.main__about-bg {
  width: 12.875rem;
  height: auto;
  bottom: 16rem;
    right: -2rem;
}
}



@media (max-width: 550px) {
  .main__about-wrap--desctop {
    display: none;
  }
  .main__about-wrap--mobile {
    display: block;
  }
  .main__about-content {
    position: relative;
    max-width: 100%;
    width: 100%;
    margin-top: 0rem;
    margin-left: 0rem;
    padding-top: 0;
  }
  .main__about-quote--wrap {
    position: relative;
    top: 0rem;
    left: 0rem;
    padding-top: 0.56rem;
  }
  .main__about-container {
    padding-left: 0.5625rem;
}
.main__about-quote {
  font-size: 0.72rem;
  margin-bottom: 0.25rem;
  line-height: 110%;
}
.main__about-author {
  font-size: 0.625rem;
}
.main__about-name {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 0.25rem;
}
.main__about-descr {
  padding-top: 0.5rem;
}
.main__about-subtitle {
  font-family: var(--font-family);
font-weight: 300;
font-size: 0.75rem;
line-height: 130%;
text-align: center;
color: var(--2-dark);
}
.main__about-content--wrap {
  padding-bottom: 17.36875rem;
  background: url('../img/about-main-mobile.png') no-repeat center top;
  background-size: 18rem;
}
.main__about-btns--desctop {
  display: block;
}
.main__about-text {
  text-align: center;
  font-size: 0.875rem;
line-height: 130%;
}
.main__about-item--text {
  font-size: 0.75rem;
  line-height: 130%;
}
.main__about-text {
  font-size: 0.875rem;
  margin-bottom: 1.125rem;
}
.main__about-item--img {
  width: 1.25rem;
  height: 1.25rem;
}
.main__about-item {
gap: 0.375rem;
}
.main__about-list {
  margin-left: 1rem;
}
.main__about-btn {
  width: 100%;
}
.main__about-list {
  margin-bottom: 1.84675rem;
}
.main__about-btn.btn-consult {
  margin-bottom: 1.2rem;
}
#main__about-btns--mobile {
  display: none;
}
.main__about {
  margin-bottom: 2.375rem;
}
.main__about-descr {
  max-width: 90%;
  margin: auto;
}
}

@media (max-width: 320px) {
  .main__about-descr {
    max-width: 100%;
    margin: auto;
  }
  .main__about-content--wrap {
    background-size: cover;
  }
}

