.tab input, .tab-content {
  display: none;
}
   .tab {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);

    // border: 1px solid #e9eaec; /* Параметры рамки */
    border-radius: 0.625rem; /* Скругляем уголки */
    // color: #848994; /* Цвет текста */


    background: rgba(165, 205, 185, 0.1);
    box-shadow: 0 0.25rem 0.625rem 0 rgba(0, 0, 0, 0.15);
   }
   .tab:not(:last-child){
    margin-bottom: 1.875rem; /* Расстояние между пунктами */
   }
   .tab-title {
    position: relative;
    padding: 1.875rem; /* Поля вокруг текста */
    display: block; /* Блочный элемент */
    cursor: pointer; /* Вид курсора */
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 130%;
    color: var(--2-dark);
   }
   .tab-title::after {
    position: absolute;
    top: 50%;
    right: 1.7rem;
    transform: translateY(-50%);
    display: inline-block;
    // content: '+'; /* Выводим плюс */
    content: ''; /* Выводим плюс */
    background: url("../img/question-arrow.svg") no-repeat center center;
    width: 3rem;
    height: 3rem;
    // float: right; /* Размещаем по правому краю */
    // margin-top: -0.5rem;
   }
   .tab-content {
    padding: 0 1.875rem 1.875rem 1.875rem; /* Поля вокруг текста */
    margin-top: -1rem;
   }
   .tab :checked + .tab-title::after {
    position: absolute;
    // top: 50%;
    top: 1rem;
    // transform: translateY(-50%);
    right: 1.7rem;
    // content: '−'; /* Выводим минус */
    content: ''; /* Выводим плюс */
    background: url("../img/question-arrow.svg") no-repeat center center;
    width: 3rem;
    height: 3rem;
    // margin-top: -0.5rem;
    transform: rotate(180deg);
   }
   .tab :checked ~ .tab-content {
    display: block; /* Показываем содержимое */
   }

   .tab :checked ~ .tab {
    box-shadow: inset 0 4px 10px 0 rgba(0, 0, 0, 0.15);
   }

   .questions {
    &__container {
      position: relative;
    }
    &__wrap {
      max-width: 66.25rem;
      margin: 0 auto;
    }
    &__bg {
      position: absolute;
      z-index: -10;
      right: 0rem;
      top: -5rem;
      width: 24.75rem;
      height: 35.6875rem;
    }
   }

   @media (max-width: 768px) {
    .tab-title {
      font-size: 1rem;
      padding: 0.8rem 1.1875rem;
      padding-bottom: 1.2rem;
    }
    .tab{
      margin-bottom: 1.25rem;
    }
    .tab:last-child {
      margin-bottom: 0;
    }
    .tab-content {
      font-size: 0.875rem;
      margin-top: -0.1rem;
      padding: 0 1.175rem 1.175rem 1.175rem;
      line-height: 130%;
    }
    .questions {
      padding-bottom: 0;
    }
    .questions__bg {
      width: 9.0625rem;
      height: auto;
      right: 1rem;
      top: -3rem;
    }
    .tab-title::after {
      right: 1.27rem;
    }
    .tab :checked + .tab-title::after {
      right: 1.27rem;
      top: 0rem;
    }
  }

  @media (max-width: 550px) {
    .questions {
      padding-top: 2.25rem;
      padding-bottom: 1.2rem;
  }
  .questions__title {
    line-height: 107%;
}
.tab-title {
  font-size: 0.875rem;
  padding: 0.8rem  0.91875rem 0.8rem 0.71875rem;
}
.tab-title::after {
  width: 1.125rem;
  height: 1.125rem;
}
.tab :checked + .tab-title::after {
  width: 1.125rem;
  height: 1.125rem;
}
.tab-content {
  font-size: 0.75rem;
  padding: 0 0.60175rem 1.175rem 0.60175rem;
}
.tab-content {
  font-size: 0.6875rem;
  margin-top: -0.1rem;

  line-height: 130%;
}
.tab:not(:last-child) {
  margin-bottom: 1.012875rem;
}
.tab-content {
  font-size: 0.77875rem;
  margin-top: -0.1rem;
  line-height: 113%;
}
.tab :checked + .tab-title::after {
  right: 0.6rem;
  top: 1rem;
}
.tab-title::after {
  right: 0.6rem;
}
.questions__bg {
  display: none;
}
  }
