.reviews{
  position: relative;
  padding-bottom: 5.2rem;
  &__bg {
    position: absolute;
    bottom: 0;
    right: -3rem;
    z-index: -1;
    width: 83.75rem;
    width: auto;
  }
  &__wrap {
    position: relative;
  }
  &__item {
    height: 31rem;
    width: auto;
    img {
      object-fit: contain;
    }
  }
}

.reviews__slider {
  &-btns {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
    display: flex;
    justify-content: space-between;
    z-index: 0;
  }
  &-btn {

    @include bg-cover;
    width: 3rem;
    height: 3rem;

    &--prev{
      background-image: url("../img/chevron-left.svg");
      margin-left: -3rem;
    }

    &--next {
      background-image: url("../img/chevron-right.svg");
      margin-right: -3rem;
    }
  }
}

@media (max-width: 768px) {
  .reviews__item {
    border-radius: 0.625rem;
    height: 22.14025rem;
}
.reviews {
  position: relative;
  padding-bottom: 3.12rem;
}
.reviews__container {
  padding: 0 4.60625rem;
}
}

@media (max-width: 550px) {
  .reviews__bg {
    display: none;
  }
  .reviews__container {
    padding: 0rem;
}
.reviews__item {
  height: 21.14025rem;
}
.reviews__slider-btns {
  top: auto;
  bottom: -3.5rem;
  justify-content: center;
  gap: 0.875rem;
}
.reviews__slider-btn--prev {
  margin-left: 0;
}
.reviews__slider-btn--next {
margin-right: 0;
}
.reviews__slider-btn {
  width: 2.25rem;
  height: 2.25rem;
  }
  .reviews {
    padding-bottom: 4.712rem;
}
}
