.contact-main {

		&__wrap {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      gap: 2.0625rem;
		}
    &__bg {
      position: absolute;
      width: 44.5rem;
      height: auto;
      bottom: 0rem;
      left: 0rem;
      z-index: -1;
    }
		&__content {
      flex: 0 0 29.0625rem;
		}

		&__title {
      text-align: left;
		}

		&__text {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.5rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 2.5rem;
		}

		&__inf {
      width: 24.375rem;
		}

		&__item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1.5rem;
      &:nth-child(5){
        margin-top: 2.5rem;
        margin-bottom: 2.6rem;
      }
		}

		&__item--name {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.25rem;
      line-height: 120%;
      color: var(--2-dark);

		}

		&__item--inf {
      font-family: var(--font-family);
      font-weight: 300;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);

		}

		&__item--map {
      font-family: var(--font-family);
      font-size: 1.25rem;
      line-height: 130%;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      color: var(--2-dark);

		}

		&__answer {
      border-radius: 0 0.625rem 0.625rem 0;
      background: #edf5f1;
      width: 100%;
      padding: 0.75rem 0.9375rem 0.75rem 0.75rem;
      font-family: var(--font-family);
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
      border-left: 0.125rem solid #A5CDB9;
		}

		&__images {
      position: relative;
      flex-grow: 1;

		}

		&__images-item {
      position: absolute;
      border-radius: 0.625rem;
      overflow: hidden;
      &:first-child{
        width: 14.5rem;
        height: auto;
        top: 0rem;
        left: 6rem;
        z-index: 10;
      }
      &:nth-child(2){
        width: 25rem;
        height: auto;
        top: 3.6rem;
        left: 12.5rem;
        z-index: -10;
      }
      &:nth-child(3){
        width: 11.25rem;
        height: auto;
        top: 9.4rem;
        right: 4.5rem;
      }
      &:nth-child(4){
        width: 20.99625rem;
        height: auto;
        top: 14.5rem;
        left: -0.5rem;
        z-index: 15;
      }
      &:nth-child(5){
        width: 22.5rem;
        height: auto;
        top: 18.8rem;
        left: 18.7rem;
        z-index: 10;
      }
      &:nth-child(6){
        width: 10.8125rem;
        height: 16.5rem;
        top: 21rem;
        right: 0rem;
        z-index: 30;
        img {
          object-fit: fill;
        }
      }
      &:last-child{
        width: 11.875rem;
        height: 14.4rem;
        top: 25rem;
        left: 13rem;
        z-index: 20;
        img {
          object-fit: fill;
        }
      }
		}
}

@media (max-width: 768px) {
  .contact-main__text {
    font-size: 1rem;
    line-height: 120%;
    margin-bottom: 1.25rem;
  }
  .contact-main__content {
    flex: 0 0 19.0625rem;
}
.contact-main__item--name {
  font-size: 1rem;
}
.contact-main__item--inf {
  font-size: 1rem;
}
.contact-main__inf {
  max-width: 18.375rem;
}
.contact-main__item {
  margin-bottom: 0.525rem;
}
.contact-main__item--map {
  font-size: 0.875rem;
}
.contact-main__item:nth-child(5) {
  margin-top: 1.15rem;
  margin-bottom: 1.26rem;
}
.contact-main__answer {
  font-weight: 300;
font-size: 0.875rem;
line-height: 130%;
}
.contact-main__answer {
  padding: 0.75rem 0.99375rem 0.575rem 0.375rem;
}

.contact-main__images-item:first-child {
  width: 7.6875rem;
  top: 2.5rem;
  left: 2rem;
}
.contact-main__images-item:nth-child(2) {
  width: 12.75rem;
  top: 4.6rem;
    left: 5.5rem;
}
.contact-main__images-item:nth-child(3) {
  width: 7.4375rem;
  top: 7.4rem;
    right: 1.95rem;
}
.contact-main__images-item:nth-child(4) {
  width: 10.75rem;
  top: 9.9995rem;
    left: -0.95rem;
}
.contact-main__images-item:nth-child(5) {
  width: 12.4375rem;
  top: 12.58rem;
    left: 7.87rem;
}
.contact-main__images-item:nth-child(6) {
  width: 6.5rem;
  height: auto;
  top: 15.5rem;
  right: 0rem;
}
.contact-main__images-item:nth-child(7) {
  width: 6.1875rem;
  top: 15.8rem;
    left: 5.8rem;
  height: auto;
}
.contact-main__images-item:nth-child(6) img {
  object-fit: cover;

}
.contact-main__images-item:last-child img {
  object-fit: cover;

}
.contact-main__bg {
  width: 44.1875rem;
}
}

@media (max-width: 550px) {
  .contact-main__wrap {
    display: block;
  }
  .contact-main__title {
    text-align: center;
    margin-bottom: 1.25rem;
  }
  .contact-main__text {
    text-align: center;
    font-size: 0.875rem;
    margin-bottom: 0.725rem;
  }
  .contact-main__item--name {
    font-size: 0.875rem;
  }
  .contact-main__item--inf{
    font-size: 0.875rem;
  }
  .contact-main__answer {
    width: 95%;
    font-size: 0.75rem;
    margin-left: 0.6rem;
    margin-bottom: 1.025rem;
  }
  .contact-main__images {
    height: 13.46875rem;
    max-width: 90%;
    margin: auto;
    left: 0.85rem;
  }
  .contact-main__images-item:first-child {
    width: 5.5625rem;
    top: 0rem;
    left: 2.5rem;
  }
  .contact-main__images-item:nth-child(2) {
    width: 9.5625rem;
    top: 1.16rem;
    left: 4.5rem;
  }
  .contact-main__images-item:nth-child(3) {
    width: 4.3125rem;
    top: 3.4rem;
    right: 1.595rem;
  }
  .contact-main__images-item:nth-child(4) {
    width: 7.875rem;
    top: 5.29995rem;
    left: 0rem;
  }
  .contact-main__images-item:nth-child(5) {
    width: 8.625rem;
    top: 6.858rem;
    left: 6.87rem;
  }
  .contact-main__images-item:nth-child(6) {
    width: 4.125rem;
    height: auto;
    top: 8.5rem;
    right: 2rem;
}
  .contact-main__images-item:nth-child(7) {
    width: 4.5625rem;
    top: 8.8rem;
    left: 4.8rem;
    height: auto;
  }
  .contact-main__inf {
    max-width: 100%;
}
}

@media (max-width: 320px) {
  .contact-main__images {
    width: 100%;
  }
  .contact-main__images-item:nth-child(6) {
    right: 0rem;
}
}
