.about-me {

  &__container {
    padding: 0 2.5rem;
  }

  &__bg {
    position: absolute;
    height: 54.50625rem;
    width: 85rem;
    z-index: -1;
  }

  &__bg img {
    width: 100%;
    height: 100%;
    object-fit: fill;
  }

  &__wrap {
    // position: relative;
    display: flex;
    align-items: center;
    gap: 2.5rem;
    padding-bottom: 2.04rem;
    // padding: 6.8125rem 2.5rem 4.125rem 2.5rem;
  }

  &__descr {
    margin: 6.28125rem 0rem 4.125rem 2.5rem;
    padding: 0;
  }

  &__title {
    text-align: left;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
  }

  &__text:not(:last-child){
    margin-bottom: 1.25rem;
  }

  &__btn {
    margin-top: 3.75rem;
    width: 18rem;
  }

  &__img {
    flex: 0 0 33.3125rem;
    margin-right: 2.5rem;
  }
  &__btn--mobile {
    display: none;
    }
  &__text a{
    text-decoration: underline;
    }
}

@media (max-width: 768px) {
  .about-me {
    margin-bottom: 3.5rem;
  }

  .about-me__container {
    position: relative;
    padding: 0px;
}
.about-me__text {
  font-size: 0.875rem;
line-height: 130%;
}
.about-me__text:not(:last-child) {
  margin-bottom: 0.625rem;
  line-height: 126%;
}
.about-me__descr {
  margin: 3.6828125rem 0rem 0rem 1.2rem;
  padding: 0;
}
.about-me__wrap {
  // position: relative;
}
.about-me__img {
  position: absolute;
  right: 2rem;
  top: 1.8rem;
  width: 20.75rem;
height: 28.3125rem;
z-index: -1;
margin-right: 0;
}
.about-me__descr {
  width: 25.375rem;
}
.about-me__bg {
  position: absolute;
  height: 43.9375rem;
  width: 45.5rem;
  z-index: -1;
  top: -4rem;
}
.about-me__container {
  padding: 0 1.25rem;
}
.about-me__wrap {
  display: block;
}
.about-me__btn--desctop {
  display: none;
}
.about-me__btn--mobile {
display: flex;
margin-top: 0rem;
margin: auto;
}
}





@media (max-width: 550px) {
  .about-me__descr {
    width: 100%;
  }
  .about-me__descr {
    margin: 0rem;
    padding: 0;
}
.about-me__title {
  text-align: center;
}
.about-me__img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  // right: 2rem;
  top: 0rem;
  width: 16.1875rem;
  height: auto;
  z-index: -1;
  margin-right: 0;
}
.about-me {
  margin-bottom: 2.5rem;
  margin-top: 2.6rem;
}
.about-me__title {
  margin-bottom: 19.5rem;
}
.about-me__text {
  font-size: 0.75rem;
}
.about-me__wrap {
  padding-bottom: 1.504rem;
}
.about-me__bg {
  position: absolute;
  height: 44.59375rem;
  width: 23.385rem;
  z-index: -1;
  top: -4rem;
  margin-left: -0.81rem;
}
.about-me__btn {
  width: 100%;
}
.about-me__text--desctop {
  display: none;
}
}
