.about {

  &__container {
    position: relative;
    padding-top: 4rem;
  }

  &__content {
    // position: relative;
    padding: 3.0025rem 0rem 6rem 0rem;
  }

  &__title {
    text-align: left;
  }

  &__wrap {
    position: relative;
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: 2rem;
    margin-bottom: 3.3rem;

    &--bottom {
      gap: 3.75rem;
      display: flex;
      align-items: start;
      justify-content: space-between;
      & .about__text:not(:last-child) {
        margin-bottom: 1.25rem;
      }
    }
  }
  &__wrap--top {
    background: url('../img/about-bg-all.svg') no-repeat center;
    background-size: cover;

  }

  &__bg {
    position: absolute;
    z-index: -5;
    width: 86rem;
    height: 37.3625rem;
    left: 2rem;
  }

  &__bg-flowers {
    position: absolute;
    width: 37.25rem;
    height: auto;
    right: -2.57rem;
    bottom: -5.2rem;
  }

  &__img {
    border-radius: 0.625rem;
    flex:0 0  39.0625rem;
    height: 23.625rem;
    &--bottom {
      border-radius: 0.625rem;
      flex: 0 0 25.4375rem;
      height: 22.3125rem;
    }
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
  }

  &__text-link{
    text-decoration: underline;
    text-decoration-skip-ink: none;
  }
}

.services-top {
  margin-top: 1.6rem;
  padding-bottom: 1rem;

  &__container {
    max-width: calc(1360px + 80px);
    padding: 0 30px;
    margin: 0 auto;
  }

  &__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    gap: 2.5rem;
    padding: 2.29625rem 0rem 0.025rem 0rem;
  }

  &__bg {
    position: absolute;
      top: 0;
      left: -3rem;
      right: 0;
      bottom: 0;
      width: 86.125rem;
      height: 36.375rem;
      z-index: -1;
    img {
      object-fit: fill;
    }
  }
  &__flowers{
    position: absolute;
    right: 2rem;
    bottom: 1rem;
    z-index: -1;
    width: 36.5625rem;
    height: 11.375rem;
  }
  &__img--wrap {
    flex: 0 0 38.6875rem;
  }

  &__img {
    border-radius: 0.625rem;
    width: 38.6875rem;
    height: auto;
  }

  &__content {
    width: 44.0625rem;
    margin-top: 7.72rem;
  }

  &__title {
    text-align: left;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    // text-align: justify;
    color: var(--2-dark);
    a {
      text-decoration: underline;
    }
  }
}

@media (max-width: 768px) {
  .about__img {
    flex: 0 0 21.0625rem;
    height: auto;
}
.about__content {
  padding: 2.825rem 0rem 3.504rem 1.2rem;
}
.about__container {
  padding-top: 1.56rem;
}
.about__text {
  font-size: 0.875rem;
}
.about__wrap {
   gap: 1.125rem;
}
.about__bg img {
  object-fit: fill;
}
.about__bg {
  width: 45.8rem;
  left: 1rem;
  height: 26.3625rem;
}
.about__img--bottom {
  flex: 0 0 17.4375rem;
  height: auto;
}
.about__wrap--bottom{
  gap: 0;
  align-items: center;
}
.about__wrap--bottom .about__text:not(:last-child) {
  margin-bottom: 0.5rem;
}
.about__bg-flowers {
  width: 14.25rem;
  right: 0.043rem;
  bottom: -3.2rem;
}
.services-top__img {
  width:20.6875rem;
  height: auto;
}
.services-top__img--wrap {
  flex: 0 0 20.96875rem;
  margin-left: 1.1rem;
}
.services-top__text {
  font-size: 0.875rem;
}
.services-top__wrap {
  gap: 1.125rem;
}
.services-top__content {
  width: 21.0625rem;
  margin-top: 5.272rem;
  width: 100%;
  padding-right: 0.43425rem;
}
.services-top__bg {
  position: absolute;
  top: 0;
  left: -0.2rem;
  right: 0;
  bottom: 0;
  width: 45.8125rem;
  height: 23.1375rem;
  z-index: -1;
}
.services-top__flowers {
  position: absolute;
  right: 0rem;
  bottom: -2rem;
  z-index: -1;
  width: 15rem;
  height: auto;
}
}

@media (max-width: 550px) {
  .services-top__wrap{
    display: block;
  }
  .services-top__wrap {
    padding: 0.829625rem 0rem 0.025rem 0rem;
}
.services-top__title {
  text-align: center;
}
.services-top__img {
  width: 16.6875rem;
  height: auto;
  margin: auto;
}
.services-top__content {
  width: 100%;
  margin-top: 0;
  padding: 0.8rem;
}
.services-top__text {
  font-size: 0.75rem;
}
.services-top__bg {
  position: absolute;
  top: -0.9rem;
  left: -0.2rem;
  right: 0;
  bottom: 0;
  width: 22.58125rem;
  height: 30.1375rem;
  z-index: -1;
}
.about__wrap--bottom {
  flex-direction: column;
  gap: 0;
  flex: 0 0 100%;
  align-items: center;
}
.about__descr {
  order: 2;
  padding-left: 0.6rem;
}
.about__text {
  font-size: 0.75rem;
}
.about__img--bottom {
  order: 1;
}
.about__content {
  padding: 2.825rem 0rem 2.504rem 0rem;
}
.about__img--bottom img{
  object-fit: contain;
}

.about__img--bottom {
  flex: 0 0 10rem;
  height: 12.5rem;
}
.about__wrap--bottom .about__text:not(:last-child) {
  margin-bottom: 0.46rem;
}

}
