.problems {

  &__wrap {
    position: relative;
  }
  &__bg {
    position: absolute;
    width: 30.9375rem;
    height: auto;
    bottom: -13rem;
    right: -3rem;
    z-index: -10;
  }
  &__title {
    max-width: 71%;
    margin: 0 auto;
    margin-bottom: 3.7rem;
  }

  &__list {
    display: flex;
    // align-items: flex-start;
    justify-content: space-between;
    gap: 1.875rem;
    flex-wrap: wrap;
  }
  &__list:not(:last-child){
    margin-bottom: 1.875rem;
  }

  &__item {
    // flex: 0 0 25.4375rem;
    width: 31.58%;
    height: 33.75rem;
    background: rgba(165, 205, 185, 0.1);
    padding: 1.875rem 1.875rem 3.125rem 1.875rem;
    border-radius: 0.625rem;
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
  }

  &__item-img {
    overflow: hidden;
    border-radius: 0.625rem;
    width: 100%;
    height: 21.6875rem;
    margin-bottom: 1.25rem;
  }

  &__item-title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 130%;
    text-align: center;
    color: var(--2-dark);
    margin-bottom: 1.25rem;
  }

  &__item-text {
    text-align: center;
    color: var(--2-4);

  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 18rem;
    margin: 0 auto;
    margin-top: 3.75rem;
  }
}

.problems__item {
  &:hover {
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
    .problems__item-title {
      transform: scale(1.02);
      transition: all 0.1s;
    }
    .problems__item-text {
      transform: scale(1.02);
      transition: all 0.1s;
    }
    .problems__item-img{
      overflow: hidden;
      img {
        transition: all 0.5s;
        transform: scale(1.05);
      }
    }
  }
  &:active {
    box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.1);

    .problems__item-title {
      transition: all 0.5s;
      transform: scale(1);
    }
    .problems__item-text {
      transition: all 0.5s;
      transform: scale(1);
    }
    .problems__item-img{
      img {
        transition: all 0.5s;
        transform: scale(1);
      }
    }
  }
}

@media (max-width: 768px) {
  .problems__list {
    gap: 1.27rem;
  }
  .problems__title {
    max-width: 90%;
    margin-bottom: 2.5rem;
    line-height: 113%;
}
.problems__item {
  width: 48.46%;
height: 20.5875rem;
padding: 1.25rem;
}
.problems__item:nth-child(5) .problems__item-title{
  margin-bottom: 0.3125rem;
}
.problems__item:nth-child(6) .problems__item-title{
  margin-bottom: 0.3125rem;
}
.problems__item-img {
  border-radius: 0.625rem;
  height: 12.5rem;
  margin-bottom: 1.025rem;
}
.problems__item-title{
  font-size: 1.25rem;
  margin-bottom: 0.875rem;
}
.problems__item-text {
  font-size: 0.875rem;
}
.problems__btn {
  margin-top: 2.75rem;
}
.problems__bg {
  position: absolute;
  width: 26.9375rem;
  height: auto;
  bottom: -11.5rem;
  right: -8rem;
  z-index: -10;
}
}

@media (max-width: 550px) {
  .problems__title {
    max-width: 100%;
    margin-bottom: 1.25rem;
    line-height: 89%;
    font-size: 1.8rem;
}
.problems__item {
  width: 100%;
  height: 15.8125rem;
  padding: 1.125rem 1.25rem;
}
.problems__item-img {
  border-radius: 10px;
  width: 100%;
height: 140px;
margin-bottom: 20px;
}
.problems__item:nth-child(4){
  display: none;
}
.problems__item:nth-child(5){
  display: none;
}
.problems__item:nth-child(6){
  display: none;
}
.problems__btn {
  margin-top: 1rem;
  width: 100%;
}
.problems__list {
  gap: 1.027rem;
}
.problems__bg {
  display: none;
}
.problems__item.problems__item-visible {
  display: block;
}
}

@media (max-width: 450px) {
.problems__item {
  width: 100%;
  height: 16.8125rem;
  padding: 1.125rem 1.25rem;
}
}


@media (max-width: 400px) {
  .problems__title {
    font-size: 2rem;
}
.problems__item {
  width: 100%;
  height: 17.8125rem;
  padding: 1.125rem 1.25rem;
}
}

@media (max-width: 370px) {
.problems__item {
  height: 18.8125rem;
}
}







