// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Noto Sans", sans-serif;
  --content-width: 1280px;
  --container-offset: 80px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // font-family
  --second-family: "Century Gothic", sans-serif;
  --third-family: "Bitter", sans-serif;
  --font3: "Inter", sans-serif;
  --font4: "Sweet Mavka Script", sans-serif;
  --font5: "Good Vibes Pro", sans-serif;

  // colors
  --2-dark: #2f3035;
  --2-light: #f7f4ed;
  --2-3: #989aa7;
  --2-white: #fff;
  --2-1: #a5cdb9;
  --2-2: #d2d9e9;
  --2-4: #6e707f;
  --2-5: #ede6d8;
}
