.certificates{
  // padding-bottom: 0;
  position: relative;
  &__wrap {
    position: relative;
  }
  &__item {
    height: 31.625rem;
    width: 22.625rem;
    img {
      object-fit: contain;
    }
  }
  &__slider {
    padding: 6.09375rem 0;
  }
  &__bg {
    position: absolute;
    z-index: -5;
    bottom: 0rem;
    left: 0rem;
    width: 63.9375rem;
    height: auto;
  }
}

.slider {
  &__btns {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
    display: flex;
    justify-content: space-between;
    z-index: 0;
  }
  &__slider-btn {

    @include bg-cover;
    width: 3rem;
    height: 3rem;

    &--prev{
      background-image: url("../img/chevron-left.svg");
      margin-left: -3.6rem;
    }

    &--next {
      background-image: url("../img/chevron-right.svg");
      margin-right: -3.6rem;
    }
  }
}

.certificates__slider .swiper-slide-active {
  transform: scale(1.2);
  transition: all 0.3s ease-in-out;
}

@media (max-width: 1439px) {
  .certificates__slider .swiper-slide-active {
    transform: scale(1.2);
  }

}
@media (max-width: 768px) {
  .certificates__item {
    border-radius: 0.625rem;
    width: auto;
    height: 14.625rem;
  }
.container__certificates {
  padding: 0 4.0625rem;
}
.certificates__slider {
  padding: 2.809375rem 0;
}
.certificates__slider .swiper-slide-active {
  transform: scale(1.4);
}
.slider__slider-btn {
  width: 3rem;
  height: 3rem;
}
.slider__slider-btn--prev {
  margin-left: -2.699rem;
}
.slider__slider-btn--next {
  margin-right: -2.699rem;
}
}

@media (max-width: 550px) {
  .container__certificates {
    padding: 0;
  }
  .slider__btns {
    top: auto;
    bottom: -4.5rem;
    justify-content: center;
    gap: 0.875rem;
  }
  .slider__slider-btn--prev {
    margin-left: 0;
}
.slider__slider-btn--next {
  margin-right: 0;
}
.certificates__slider {
      padding: 3.0180937rem 0;
}
.slider__slider-btn {
width: 2.25rem;
height: 2.25rem;
}
.certificates {
  padding-bottom: 5.7rem;
}
.certificates__bg {
  bottom: 4rem;
  width: 20rem;
}
.certificates__slider .swiper-slide-active {
  transform: scale(1.2);
}
}

