html {
  box-sizing: border-box;
  font-size: 1.10990335vw;
}
@media (max-width: 768px) {
  html {
    box-sizing: border-box;
    font-size: 2.1vw;
  }
}
@media (max-width: 550px) {
  html {
    box-sizing: border-box;
    font-size: 4.1vw;
  }
}
@media (max-width: 320px) {
  html {
    box-sizing: border-box;
    font-size: 5.06991vw;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
p {
  margin: 0;
  padding: 0;
}
ul, li {
  padding: 0;
  margin: 0;
}
h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
address {
  font-style: normal;
}
a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 1rem;
  color: var(--2-dark);
  font-weight: 400;
  font-family: var(--font-family);
  line-height: 130%;
}
@media (max-width: 768px) {
  .page__body {
    font-size: 0.875rem;
  }
}

.section {
  position: relative;
  padding: 6.25rem 0;
}

.main-title {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 3.25rem;
  line-height: 120%;
  text-align: center;
  color: var(--2-4);
  margin-bottom: 3.75rem;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
.btn {
  position: relative;
  border-radius: 0.625rem;
  height: 3.25rem;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);
  background: var(--2-4);
  font-family: var(--font-family);
  font-size: 1.25rem;
  color: var(--2-white);
  overflow-x: hidden;
  &:hover {
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);
    background: var(--2-3);
  }
  &:active {
    box-shadow: inset 0 2px 8px 0 rgba(0, 0, 0, 0.25);
    background: var(--2-3);
  }
}

.glare:after {
  content: " ";
  background: url(../img/arrow-menu.svg) no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
    top: 0;
    height: 100%;
    width: 45px;
    transform: skewX(-45deg);
    animation: flareAnimation;
    left: -150%;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.4));
    animation: flareAnimation 3s infinite linear;

}
@keyframes flareAnimation {
  0% {
      left: -150%;
  }
  100% {
      left: 150%;
  }
}

.btn-link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.mobile {
  display: block;
}
.desctop {
  display: none;
}

@media (max-width: 768px) {
  .container {
    margin: 0 auto;
    padding: 0 20px;
    // max-width: var(--container-width);
    max-width: calc(768px + 40px);
  }
}
@media (max-width: 320px) {
  .container {
    margin: 0 auto;
    padding: 0 9px;
    max-width: calc(320px + 18px);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex-all-sb {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


@media(min-width:1440px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 768px)  {
  .btn {
    font-size: 1rem;
  }
  .section {
    padding: 3.75rem 0;
  }
  .main-title {
    margin-bottom: 2.5rem;
   font-size: 2.5rem;
  }
}

@media (max-width: 550px){
  .main-title {
    font-size: 2rem;
    margin-bottom: 1.25rem;
}
}


@media (max-width: 320px) {
  .btn {
    height: 2.625rem;
    font-size: 1rem;
  }
  .main-title {
    font-size: 1.5rem;
    margin-bottom: 1.25rem;
  }
  .section {
    padding: 2.5rem 0;
  }
}

