.header {
  background: var(--2-light);
  padding: 0.65rem 0 1rem;
  position: relative;
  z-index: 100;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 13.1875rem;
    height: 4.6875rem;
    margin-right: 1rem;
  }
  &__contact {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    width: 31.875rem;
  }
  &__social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
  }
  &__contact-item {
    display: flex;
    align-items: center;
    gap: 0.625rem;
  }
  &__contact-item:first-child {
    margin-bottom: 0.95rem;
  }
  &__contact-address {
    img {
      width: 1.525rem;
      height: auto;
    }
    address {
      font-style: normal;
    }
  }
  &__contact-email img{
    width: 1.375rem;
    height: auto;
  }
  &__contact-time img{
    width: 1.375rem;
    height: auto;
  }
  &__contact-phone {
    img{
      width: 1.375rem;
      height: auto;
    }
    a {
      font-weight: 500;
      font-size: 1.5rem;
    }
  }
  &__border {
    width: 100%;
    height: 0.0625rem;
    background: var(--2-1);
    border-radius: 0.5rem;
    margin: 0.5625rem 0 1.01875rem;
  }

  &__burger {
    display: none;
    width: 1.5rem;
    height: 1.375rem;
    cursor: pointer;
  }
}
#header-btn {
  width: 13.75rem;
}

.header__nav {
  &-list {
    display: flex;
    align-items: center;
    gap: 2.5rem;
    margin-left: 7rem;
    font-size: 1.25rem;
  }
  &-item {
    cursor: pointer;
    &--down:nth-child(3)::after {
      content: " ";
      background: url("../img/arrow-menu.svg") no-repeat;
      // background: url("../img/chevron-down.svg") no-repeat;

      background-position: center;
      background-size: contain;
      width: 1rem;
      height: 1rem;
      display: inline-block;
      position: absolute;
      right: -1rem;
      top: 0.35rem;
    }
  }
}
.social-link {
  width: 1.5rem;
  height: 1.5rem;
}

// выпадающее меню
// .header__nav-item:nth-child(3) {
//   position: relative;
// }
.header__nav-item--down {
  position: relative;
  padding-right: 1rem;
  margin-right: 1rem;
}
.nav__drop-down-list {
  display: none;
  position: absolute;
  top: 1.43523rem;
  left: -1rem;
  width: 16rem;
  background: var(--2-light);
  padding: 1rem;
  border-radius: 0 0 0.65rem 0.65rem;
  // box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.25);
  z-index: 0;
}

.nav__drop-down-item a{
  font-size: 1.15rem;
}
