.nav__drop-down-item:not(:last-child) {
  margin-bottom: 0.5rem;
}
.header__nav-item--down:hover .nav__drop-down-list{
  display: block;
  transition: all 0.5s;
}

.header__nav-item:nth-child(3):hover::after {
  transform: rotate(180deg);
  padding-top: 1rem;
}

.nav__link {
  position: relative;
}
.nav__link.active{
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: -0.125rem;
    left: 0;
    display: block;
    width: 100%;
    height: 0.0625rem;
    background: var(--2-3);
    padding-top: 0.1rem;
  }
}
.header__nav-item:hover > .nav__link::after {
  content: "";
  position: absolute;
  bottom: -0.125rem;
  padding-top: 0.1rem;
  left: 0;
  display: block;
  width: 100%;
  height: 0.0625rem;
  background: var(--2-3);
}

// mobile burger

.header__nav-mobile {
  display: none;
  position: absolute;
  right: -31.25rem;
  top: 0;
  z-index: 5000;
  transition: all 0.5s ease;
}

.header__nav-mobile.active {
  right: 0;
  display: block;
  transition: all 0.5s ease;
}
.header__nav-mobile--wrapper.active {
  width: 100%;
  height: 100vh;
  // position: absolute;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  transition: all 0.5s ease;
}


.header__nav-mobile--content {
  border-radius: 0.625rem;
  width: 20rem;
  background: var(--2-light);
  padding: 2.5rem;
  .header__nav-list {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: 1.25rem;
    margin: 0;
  }
  .modal__close {
    top: 1.25rem;
    right: 1.25rem;
    cursor: pointer;
  }
  .header__contact-wrapper {
    margin-bottom: 2.5rem;
  }
  .header__nav {
    margin-bottom: 2.5rem;
  }
  .header__contact-item {
    gap: 0.4625rem;
  }
  .header__contact-item:first-child {
    margin-bottom: 0.875rem;
  }
  .header__btn {
    width: 100%;
  }
  .header__nav-item:nth-child(3)::after {
    background-position: right 0.242rem;
  }
}

@media (max-width: 768px) {
  .header__burger {
    display: block;
  }
  .header__nav-mobile{
    display: block;
  }
  #header__nav--mobile {
    display: block;
  }
  .header__contact-wrapper:first-child {
    display: none;
  }
  #header-btn {
   display: none;
  }
  .header__contact {
    width: 13.5875rem;
  }
  .header__logo {
    width: 10.375rem;
    height: auto;
    margin-right: 0rem;
  }
  .header {
    padding: 0.65rem 0 0.41rem;
}
  .header__border {
    display: none;
  }
  .header__nav {
    display: none;
  }
  .header__contact-phone a {
    font-size: 1.25rem;
  }
  .header__contact-time p {
    font-size: 0.875rem;
  }
  .header__contact-wrapper {
    width: 14.5625rem;
  }
  .header__contact-item:first-child {
    margin-bottom: 0.5rem;
}
.header__burger {
  display: block;
  margin-right: 0.8rem;
}
.nav__link {
  font-size: 1.25rem;
}
.header__contact-wrapper {
  font-size: 0.8999812rem;
}
.header__social--mobile {
  display: none;
}
.header__contact-wrapper--mobile {
  display: none;
}
.nav__drop-down-list {
  position: static;
}
.nav__drop-down-list {
  padding-left: 0;
}
.header__nav-item--down:nth-child(3)::before {
  content: " ";
  background: url(../img/arrow-menu.svg) no-repeat;
  background-position: center;
  background-size: contain;
  width: 1rem;
  height: 1rem;
  display: inline-block;
  margin-left: 0;
  position: absolute;
  top: 0.67rem;
  transform: translateY(-50%);
  left: 5rem;
}
.header__nav-item:nth-child(3):hover::before {
  transform: rotate(180deg);
  top: 0.2rem;
}
.header__nav-item--down:nth-child(3)::after {
  display: none;
}
.header__nav-item--down {
  position: relative;
}
.nav__drop-down-list {
  padding-bottom: 0;
}
}

@media (max-width: 550px) {
  .header__social--desctop {
    display: none;
  }
  .header__social.header__social--mobile {
    justify-content: start;
    display: flex;
    gap: 0.5rem;
  }
  .header__social  {
    margin-bottom: 2rem;
  }
  .header__contact-wrapper--desctop {
    display: none;
  }
  .header__contact-wrapper--mobile {
    display: block;
  }
  .header__burger {
    width: 1.35rem;
    height: auto;
    margin-right: 0.9rem;
}
  .header__logo {
    width: 7.125rem;
    height: 40px;
    height: 2.5rem;
  }
  .header {
    padding: 0.625rem 0 ;
    // padding-right: 1.375rem;
}
.header__contact {
  display: none;
}
.header__nav-mobile--content .header__contact-item:first-child {
  margin-bottom: 0.575rem;
}

.header__nav-mobile--content .header__contact-wrapper {
  margin-bottom: 1.25rem;
}
.header__nav-mobile--content .header__contact-wrapper.nav__drop-down-item:last-child {
  margin-bottom: 0rem;
}

}
