.footer {
  // position: relative;
  margin-top: 6.025rem;
  padding: 6.25rem 0 2.5rem;
  background: var(--2-2);
  // z-index: -1;

  &__container {
    position: relative;
  }

  &__bg {
    position: absolute;
    z-index: 0;
  }

  &__bg--left {
    bottom: -3.1rem;
    left: -1rem;
    width: 34.25rem;
    height: auto;
  }

  &__bg--right {
    bottom: -2.85rem;
    right: -0.1rem;
    width: 30.0625rem;
    height: auto;
  }

  &__wrap {
    position: relative;
    display: flex;
    align-items: start;
    justify-content: space-between;
    z-index: 10;
  }

  &__item {
    &:first-child{
      flex: 0 0 17.99625rem;
    }
    &:nth-child(2){
      flex: 0 0 11.625rem;
    }
    &:nth-child(3){
      flex: 0 0 14.8rem;
    }
    &:nth-child(4){
      flex: 0 0 18.4375rem;
    }
    &:not(:last-child) {
      margin-bottom: 1.25rem;
    }
  }

  &__services-title a{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.25rem;
    line-height: 120%;
    color: var(--2-dark);
  }

  &__logo {
    width: 13.1875rem;
    height: auto;
    margin-bottom: 2.3rem;
    margin-top: -1rem;
  }

  &__reg {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.75rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-bottom: 3.0075rem;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }

  &__social-item {
    width: 2rem;
    height: 2rem;
  }

  &__item-link {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 130%;
    color: var(--2-dark);
    // &:hover{
    //   border-bottom: 1px solid var(--2-dark);
    // }
  }

  &__pay-title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.25rem;
    line-height: 120%;
    color: var(--2-dark);
    margin-bottom: 1.625rem;
  }

  &__pay-content {
    margin-bottom: 1.543125rem;
    width: 15.3rem;
  }

  &__pay-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child){
      margin-bottom: 1.025rem;
    }
  }

  &__pay-inf--title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1rem;
    line-height: 130%;
    color: var(--2-dark);
    margin-bottom: 0.8rem;
  }

  &__pay-link {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.875rem;
    line-height: 130%;
    color: var(--2-dark);
    &:not(:last-child){
      margin-bottom: 0.5rem;
    }
  }

  &__copyright {
    margin-top: 1.25rem;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 130%;
    text-align: center;
    color: var(--2-dark);
    margin-bottom: 0.625rem;
  }

  &__descr {
    display: flex;
    align-items: end;
    justify-content: end;
  }

  &__descr-text {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.75rem;
    line-height: 130%;
    text-align: center;
    color: var(--2-4);
    flex: 0 0 26.8125rem;
    margin-right: 16.25rem;
  }

  &__descr-developer {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 0.875rem;
    line-height: 130%;
    text-align: right;
    color: var(--2-4);
  }

  &__pay-li:not(:last-child) {
    margin-bottom: 0.3rem;
  }
  &__pay-content--mobile {
    display: none;
  }
  &__lists-item:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}


.footer__logo--mobile {
  display: none;
}

@media (max-width: 768px) {
  .footer {
    margin-top: 3.997025rem;
    padding: 3.5825rem 0 2.5rem;
}
.footer__logo {
  width: 14.2991875rem;
  height: auto;
  margin-bottom: 0.993rem;
  margin-top: 0rem;
}
.footer__reg {
  line-height: 118%;
  margin-bottom: 1.20075rem;
}

.footer__wrap {
  flex-wrap: wrap;
}
.footer__item-link {
font-size: 1rem;
}
.footer__item {
  margin-bottom: 1rem;
}
.footer__item:nth-child(2) {
  flex: 0 0 8.625rem;
}
.footer__item:nth-child(3) {
  flex: 0 0 12.58rem;
}
.footer__item-link {
  line-height: 100%;
}
.footer__pay {
  display: flex;
  gap: 2.25rem;
  margin-top: 0.7rem;
}
.footer__pay-inf {
  flex: 0 0 15rem;
  order: 1;
}
.footer__pay-content {
  order: 2;
  margin-bottom: 1.543125rem;
  width: 33.83rem;
}
.footer__pay-title {
  font-size: 1rem;
  margin-bottom: 1.25rem;
}
.footer__pay-content--desctop {
  display: none;
}
.footer__pay-content--mobile {
  display: block;
}
.footer__pay-item:not(:last-child) {
  margin-bottom: 0.5025rem;
}
.footer__bg--left {
  bottom: -2.1rem;
  left: -0.3rem;
  width: 17.25rem;
  height: auto;
}
.footer__copyright {
  margin-top: 0.25rem;
}
.footer__descr {
  display: block;
}
.footer__descr-developer {
  display: block;
  text-align: right;
}
.footer__descr-text {
  margin-right: 0;
  max-width: 55%;
    margin: 0 auto;
}
.footer__descr-developer {
  font-size: 0.75rem;
  margin-top: 1.25rem;
}
.footer__bg--right {
  width: 22rem;
  bottom: -3.585rem;
    right: 1.9rem;
}
.footer__services-title a {
  font-size: 1rem;
}
}

@media (max-width: 550px) {
  .footer {
    margin-top: 3.7025rem;
    padding: 2.55825rem 0 2.5rem;

}
.footer__logo{
margin-left: 0.52rem;
}
.footer__logo--desctop {
  display: none;
}
.footer__logo--mobile {
  display: block;
  margin-bottom: 1.42rem;
}
.footer__reg {
  order: 2;
}
.footer__social {
  order: 1;
  margin-bottom: 20px;
}

.footer__item:nth-child(1) {
  flex: 0 0 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__item-link  {
  font-size: 14px;
}
.footer__services-title a {
  font-size: 14px;
}
.footer__lists-item:not(:last-child) {
  margin-bottom: 0px;
}
.footer__item-link {
  margin-bottom: 12px;
}

.footer__item:nth-child(1){
  order: 3;
  margin-bottom: 0rem;
}
.footer__item:nth-child(2){
  order: 1;
  flex: 0 0 7.625rem;
  margin-bottom: 1.5rem;
}
.footer__item:nth-child(3){
  order: 2;
}
.footer__item:nth-child(4){
  order: 4;
  flex: 0 0 100%;
}
.footer__social-item {
  width: 24px;
height: 24px;
}
.footer__pay {
  display: block;
  margin-bottom: -0.5rem;
}
.footer__pay-content {
  width: 100%;
}
.footer__pay-inf  {
  width: 100%;
  text-align: center;
}
.footer__pay-title, .footer__pay-inf--title {
  text-align: center;
  font-size: 14px;
}
.footer__pay-item {
  gap: 0.4rem;
}
.footer__pay-link {
  font-size: 12px;
}
.footer__pay-content {
  margin-bottom: 2.26254313rem;
}
.footer__pay-li:not(:last-child) {
  margin-bottom: 0.13rem;
}
.footer__copyright {
  font-size: 12px;
  margin-bottom: 6px;
}
.footer__descr-text {
  font-size: 12px;
  width: 100%;
  max-width: 100%;
}
.footer__descr-developer {
  text-align: center;
}
.footer__pay-inf {
  margin-bottom: 0.7rem;
}
.footer__bg--left {
  display: none;
}
.footer__bg--right {
  width: 266px;
  bottom: 30.415rem;
  right: -0.1rem;
    transform: scaleX(-1);
}
.footer__copyright {
  margin-top: 1.625rem;
}
.footer__item:nth-child(3) {
   flex: 0 0 10.58rem;
  }
  .footer__pay {
    margin-top: 0rem;
}
.footer__wrap {
  flex-wrap: wrap;
  justify-self: start;
  gap: 2rem;
}
}

@media (max-width: 320px) {
  .footer__wrap {
    flex-wrap: wrap;
    justify-self: start;
    gap: 0rem;
  }
}
