
.body {

		&__wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
		}

		&__content {
      // margin-top: 6.4rem;
		}

		&__title {
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 4.125rem;
      line-height: 110%;
      color: var(--2-4);
      margin-bottom: 3rem;
		}

		&__descr {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.5rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 3.5rem;
      // min-height: 11.125rem;
		}

		&__img {
      flex: 1 0 44.625rem;
      height: 37.5rem;
      img {
        object-fit: contain;
      }
		}
    &__container {
      // padding-left: max(80px, (100% - var(--content-width)) / 2);
      padding-right: 0;
    }
    &__descr--service-3 {
      // margin-bottom: 8.7rem;
    }
    &__title--service-4 {
      // margin-bottom: 2.5rem;
    }
    &__descr--service-4 {
      // margin-bottom: 4.6rem;
    }
    &__title--services-5 {
      margin-bottom: 2.5rem;
    }
    &__descr--services-5 {
      margin-bottom: 2.75rem;
    }
    &__descr--service-6 {
      margin-bottom: 4.8rem;
    }
    &__about-btns--mobile {
      display: none;
    }
}

.work {
  position: relative;

		&__bg {
      position: absolute;
      z-index: -1;
      bottom: 0rem;
      left: 35rem;
      width: 41rem;
      height: auto;
		}

		&__container {
      position: relative;
      max-width: 66.25rem;
      margin: 0 auto;
		}

		&__flowers {
      position: absolute;
      z-index: -1;
      top: 4rem;
      left: 0rem;
      width: 36.75rem;
      height: auto;
		}

		&__title {
      margin-bottom: 2.4625rem;
  }

		&__quote {
      text-align: right;
      white-space: pre;
      font-family: var(--third-family);
      font-weight: 400;
      font-size: 1.875rem;
      line-height: 100%;
      text-align: right;
      color: var(--2-dark);
      margin-bottom: 2.5rem;
      span {
        display: block;
        font-family: var(--font-family);
        font-weight: 300;
        font-size: 1rem;
        line-height: 130%;
        text-align: right;
        color: var(--2-dark);
        margin-top: 0.625rem;
      }
		}
    &__quote--first {
      margin-bottom: 1.25rem;
    }
		&__content-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 1.375rem;
      margin-bottom: 2.5rem;
		}
		&__text {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 1.25rem;
      &:last-child {
        margin-bottom: 0;
      }
		}

		&__content-text {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 0;
		}

    &__content-img {
      flex: 0 0 36.5625rem;
      height: 24.375rem;
    }
		&__text-selection {
      max-width: 61.375rem;

      margin: 1.875rem 0 1.875rem 4.9375rem;
      border-radius: 0 0.625rem 0.625rem 0;
      padding: 1.0625rem 0.75rem;
      background: #edf5f1;
      border-left: 0.125rem solid var(--2-1);
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
		}

    &__text--service-1 {
      margin-top: -1.35rem;
    }

    &__container--services-4 {
      position: relative;
    }
    &__text--service-4 {
      padding-top: 3rem;
    }
    &__flowers--service-4 {
      top: -3.5rem;
      left: -11rem;
    }
    &__content-img--services-5 {
      flex: 0 0 34.5625rem;
      height: auto;
    }
    &__text--service-6 {
      margin-bottom: 1rem;
    }
    &__link {
      text-decoration: underline;
    }
    &__flowers--mobile {
      display: none;
    }
}
.body__title--mobile {
  display: none;
}

@media (max-width: 768px) {
  .body__title {
    font-size: 3.25rem;
    line-height: 120%;
    margin-bottom: 2.5rem;
  }
  .body__descr {
    font-size: 1rem;
  }
  .body__img {
    flex: 0 1 21.1875rem;
    height: 17.8125rem;
  }
  .body__content {
    width: 62%;;
  }
  .body__wrap {
    gap: 0;
    margin-bottom: 2.4rem;
  }
  .body__about-btns--desctop {
    display: none;
  }
  .body__about-btns--mobile {
    display: flex;
    justify-content: center;
    gap: 1.875rem;
    margin-left: -1rem;
  }
  .body__content {
   margin-top: 0rem;
}
  .body__descr {
    margin-bottom: 0rem;
}
.body__wrap--mobile {
  align-items: center;
}
.body__content--mobile {
  margin-top: 0;
}

.work__quote {
  font-size: 1.125rem;
  margin-bottom: 1.0995rem;
}
.work__quote span {
  font-size: 0.75rem;
}
.work__title {
  margin-bottom: 1.11199625rem;
}
.work__quote span {
  margin-top: 0.2625rem;
}
.work__content-img {
  flex: 0 0 23.375rem;
  height: 15.5625rem;
}

.work__text {
  font-size: 0.875rem;
  line-height: 129%;
  margin-bottom: 0.52rem;
}
.work__content-text {
  font-size: 0.875rem;
}
.work__content-wrap {
  margin-bottom: 0.995rem;
  gap: 1.1375rem;
}
.work__content-text {
  margin-bottom: 0;
}
.work__text-selection{
  font-size: 0.875rem;
  padding: 0.6875rem 0.5rem;
}
.work__text-selection {
  margin: 0.875rem 0 1.2875rem 1.289375rem;
}
.work {
  padding-bottom: 2.5rem;
}
.work__flowers {
  width: 24.625rem;
  height: auto;
  top: 1.5rem;
}
.work__text--service-1 {
  margin-top: 0;
}
.body__title--mobile {
  display: none;
}
}

@media (max-width: 550px) {
  .body__wrap {
   flex-direction: column;
  }
  .body__content {
    width: 100%;
  }
  .body__title--mobile {
    font-size: 2rem;
    text-align: center;
    display: block;
  }
  .body__descr {
    font-size: 0.8125rem;
    text-align: center;
    line-height: 142%;

  }
  .body__title--desctop {
    display: none;
  }
  .body__content {
    order: 2;
  }
  .body__img {
    order: 1;
  }
  .body__container {
   padding-right: 20px;
   margin-top: 2.5rem;
}

.body__img {
  flex: 0 1 15.1875rem;
  height: auto;
}
.body__title {
  margin-bottom: 0.5rem;
}
.body__content {
  margin-top: 0.864rem;
}
.body__about-btns--mobile {
  display: block;
  width: 100%;
  margin-left: 0;
}
.body__wrap {
  gap: 0;
  margin-bottom: 1.8rem;
}
.work__quote {
  font-size: 1rem;
}
.work__quote span {
  margin-top: 0.2625rem;
  font-size: 0.625rem;
}
.work__content-wrap {
  display: block;
}
.work__text {
  width: 100%;
  font-size: 0.75rem;
  line-height: 137%;
}
.work__content-img {
  width: 100%;
}
.work__content-img {
  width: 18.75rem;
height: 12.4375rem;
margin: auto;
margin-bottom: 1.80008rem;
}
.work__content-text {
  margin-bottom: 1.5rem;
  line-height: 131%;
}
.work__text-selection {
  font-size: 0.75rem;
}
.work__flowers {
  width: 9.25rem;
  height: auto;
  top: 6.5rem;
}
.work__flowers--mobile {
  display: block;
  position: absolute;
  top: 47rem;
  right: -0rem;
  z-index: -1;
  width: 14.375rem;
  height: auto;
}
.work__text-selection {
  margin: 0.875rem 0 1.2875rem 1.8289375rem;
}
.work__quote {
  margin-bottom: 0.80995rem;
}
.body__title {
  font-size: 2rem;
  text-align: center;
  // margin-bottom: 1.25rem;
}
}





