.map {
  margin-top: 6.025rem;

  &__container {
    position: relative;
  }

  &__bg {
    position: absolute;
    z-index: -1;
    width: 44.5625rem;
    height: auto;
    top: -8rem;
    left: 10rem;
  }
  &__wrap iframe{
    width: 100%;
  }
}
#map iframe{
  height: 31.125rem;
}
@media (max-width: 768px) {
  .map {
    margin-top: 3.8025rem;
  }

.map__bg {
  width: 28.125rem;
  height: auto;
  top: -6rem;
    left: 4rem;
}
#map iframe {
  height: 17.125rem;
}
}
@media (max-width: 550px) {
  #map iframe {
    height: 17.125rem;
}
}
