.services-about {
  margin-top: 1.6rem;
  padding-bottom: 1rem;

  &__container {
    max-width: calc(1360px + 80px);
    padding: 0 30px;
    margin: 0 auto;
  }

  &__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    gap: 2.5rem;
    padding: 3.9625rem 3.99025rem 5.25rem 1.9525rem;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 36.375rem;
    z-index: -1;
    img {
      object-fit: fill;
    }
  }
  &__flowers{
    position: absolute;
    right: 2rem;
    bottom: 1rem;
    z-index: -1;
    width: 36.5625rem;
    height: 11.375rem;
  }

  &__img {
    border-radius: 0.625rem;
    flex: 0 0 34.6875rem;
    height: 26.5rem;
  }

  &__content {
    width: 44.0625rem;
    margin-top: 1.2rem;
  }

  &__title {
    text-align: left;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 130%;
    text-align: justify;
    color: var(--2-dark);
    a {
      text-decoration: underline;
    }
  }
}
.services-about__title--mobile {
  display: none;
}

@media (max-width: 768px) {
.services-about__img {
  flex: 0 0 20.820625rem;
  height: 16.125rem;
}
.services-about__text {
  font-size: 0.875rem;
}
.services-about__container {
  padding: 0 1.25rem;
}
.services-about__wrap {
  gap: 0.575rem;
  padding: 2.869625rem 0.99025rem 1.525rem 0.9525rem;
  margin-top: 5rem;
}
.services-about {
  margin-top: 3.6rem;
  padding-bottom: 2.0rem;
}
.services-about__content {
  margin-top: -0.49rem;
}
.services-about__bg {
  height: 22.375rem;
}
.services-about__flowers{
  width:17.875rem;
  height: 5.5625rem;
  right: 0rem;
  bottom: 13rem;
}

}

@media (max-width: 550px) {
  .services-about__wrap {
    display: block;
  }
  .services-about__title--mobile {
    display: block;
    text-align: center;
  }
  .services-about__title--desctop {
    display: none;
  }
  .services-about__wrap {
    margin-top: 0rem;
    padding: 1.2986962rem 0.099025rem 1.525rem 00.09525rem;
}
.services-about__content {
  width: 100%;
}
.services-about__text {
  font-size: 0.75rem;
  padding: 0.9rem 0.5rem;
}
.services-about__img {
  height: 12.0125rem;
  width: auto;
}
.services-about__img img {
  object-fit: contain;
}
.services-about__container {
  padding: 0 0.5625rem;
}
.services-about__bg {
  height: 32.375rem;
  top: -2rem;
}
.services-about__flowers {
  width: 10.125rem;
  height: auto;
  bottom: 26rem;
}
.services-about {
  padding-bottom: 0.2rem;
}

}
