.price {

		&__wrap {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 1.875rem;
      padding-bottom: 6.25rem;
		}

		&__item {
      position: relative;
      border-radius: 0.625rem;
      flex: 0 0 25.34375rem;
      height: 36.5625rem;
      padding: 2.49875rem 1.25rem 6rem 1.25rem;
      background: #f0f1f2;
      box-shadow: 0 0.25rem 0.625rem 0 rgba(0, 0, 0, 0.15);
      text-align: center;
		}

		&__item-title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.5rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-4);

		}

		&__item-input--text {
      display: block;
      width: 100%;
      text-align: center;

      padding: 0 0.9375rem;
      height: 3.25rem;
      outline: none;
      border: none;
      border-radius: 0.625rem;
      margin-bottom: 4.625rem;
      font-family: var(--font-family);
      font-weight: 300;
      font-size: 1.25rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-3);
      background: var(--2-white);
		}

    &__item-input {
      width: 8.75rem;
      height: 8.75rem;
      border-radius: 50%;
      margin: 0 auto;
      font-family: var(--second-family);
      font-weight: 700;
      line-height: 120%;
      text-align: center;
      color: var(--2-4);
      margin-bottom: 1.875rem;
      background: var(--2-white);
      font-size: 2.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 60%;

      .byn {
        font-size: 1.5rem;
      }
      .eur {
        font-size: 1.5rem;
        font-weight: 700;
        color: var(--2-dark);
      }
    }

		&__item-subtitle {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.25rem;
      line-height: 120%;
      text-transform: uppercase;
      text-align: center;
      color: var(--2-4);
      margin-bottom: 1.6875rem;
		}

		&__item-text {
      font-family: var(--font-family);
      font-weight: 300;
      font-size: 1.25rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-dark);
      margin-bottom: 1.75rem;
      span {
      display: block;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.25rem;
      line-height: 120%;
      text-align: center;
      color: var(--2-dark);
      }
		}

		&__item-anonymity {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      text-align: center;
      color: var(--2-dark);
      margin-bottom: 2.5rem;
      }

		&__item-btn {
      position: absolute;
      width: 22.9375rem;
      bottom: 1.875rem;
      left: 50%;
      transform: translateX(-50%);
		}
}

.price__item .price__item-title{
  margin-bottom: 2.3875rem;
}

.price__item:first-child .price__item-title{
  margin-bottom: 5.99625rem;
  margin-top: 1rem;
}
.price__item:nth-child(2) {
  background: var(--2-light);
}
.price__item:nth-child(3) {
  background: #edf5f1;
}
.price__item:nth-child(4) {
  background: #f2f4f8;
}
.price__item:nth-child(5) {
  background: #fdf8ed;
  padding-top: 1.5rem;
}
.price__item:nth-child(5) .price__item-title {
  margin-bottom: 1.3875rem;
}
.price__item:nth-child(4) .price__item-text {
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 1rem;
  line-height: 130%;
  text-align: center;
  color: var(--2-dark);
  margin-bottom: 0.5075rem;
}
.price__item:nth-child(5) .price__item-text {
  margin-bottom: 1.395075rem;
}

@media (max-width: 768px) {
  .price__wrap {
    gap: 1.3875rem;
