  }
  .price__item {
    flex: 0 0 48%;
    height: 31.45625rem;
    padding: 1.8949875rem 1.25rem 6rem 1.25rem;
  }
  .price__item-title {
    font-size: 1.25rem;
  }
  .price__item-btn {
    width: 19.5rem;
    bottom: 1.2875rem;
  }
  .price__item:first-child .price__item-title {
    margin-bottom: 5.599625rem;
}
.price__item-input--text {
  font-size: 0.9925rem;
  margin-bottom: 3.9625rem;
}
.price__item-subtitle {
  font-size: 1rem;
}
.price__item-text {
  font-size: 1rem;
  margin-bottom: 1.75rem;
}
.price__item-text span  {
  font-size: 1rem;
}
.price__item-anonymity {
  font-size: 1rem;
}
.price__item-input {
    margin-bottom: 0.99875rem;
  }
  .price__item:nth-child(4) .price__item-text{
    font-size: 0.875rem;
  }
  .price__item-input {
    font-size: 1.7rem;
  }
  .price__item-textarea {
    padding-top: 2.542rem;
    font-size: 1.5788125rem;
    line-height: 108%;
}

.price__item-input {
  font-size: 2.5rem;
  line-height: 60%;
  .byn {
    font-size: 1.5rem;
  }
  .eur {
    font-size: 1.5rem;
  }
}
}
@media (max-width: 550px) {
  .price__title {
    font-size: 2rem;
    line-height: 120%;
  }
  .price__item {
    flex: 0 0 100%;
    height: 27.3875rem;
    padding: 1.8949875rem 0.6025rem 6rem 0.6025rem;
  }
  .price__item-btn {
    width: 17.5rem;
}
.price__wrap {
  gap: 0.796863875rem;
}
.price__item:first-child .price__item-title {
  margin-bottom: 5.11599625rem;
}
.price__item-input--text {
  height: 2.525rem;
}
.price__item-input--text {
  height: 2.51525rem;
  margin-bottom: 3.29625rem;

}
.price__item-subtitle {
  margin-bottom: 0.9926875rem;
}
.price__item-text {
  margin-bottom: 1.075rem;
}
.price__item-input {
  width: 6.7rem;
  height: 6.7rem;
}
.price__item-input {
  font-size: 2.25rem;
}
.price__item-input {
  font-size: 1.24625rem;
}
.price__item-textarea {
  font-size: 1.15rem;
  padding-top: 2rem;
}
.price__item:nth-child(5) {
  background: #fdf8ed;
  padding-top: 0.725rem;
}
.price__wrap {
  padding-bottom: 2.425rem;
}
.price__item:first-child .price__item-title {
  margin-bottom: 4.99625rem;
  margin-top: 0.8rem;
}

.price__item-input {
  font-size: 2rem;
  line-height: 60%;
  .byn {
    font-size: 1.25rem;
  }
  .eur {
    font-size: 1.25rem;
  }
}
  .price__item:nth-child(4) .price__item-title {
      margin-bottom: 1.503875rem;
    background: #f2f4f8;
  }
}

