.cookies {
  position: fixed;
  width: 100%;
  z-index: 500;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: white;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5625rem 0;
    gap: 2.75rem;
  }

		&__text {
      width: 43.14375rem;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1rem;
      line-height: 130%;
      color: var(--2-dark);
		}

		&__btns {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 1rem;
		}

		&__btn-accept {
      border-radius: 0.625rem;
      width: 12.5rem;
      height: 2.375rem;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1rem;
      line-height: 130%;
      color: var(--2-white);
		}

		&__btn-refuse {
      border: 1px solid var(--2-3);
      border-radius: 0.625rem;
      width: 12.5rem;
      height: 2.375rem;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1rem;
      line-height: 130%;
      color: var(--2-3);
      background: none;
      &:hover {
        color: #fff;
      }
		}

		&__btn-details {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1rem;
      line-height: 130%;
      text-decoration: underline;
      color: var(--2-3);
		}
}

@media (max-width: 768px) {
  .cookies__wrapper {
    display: block;
  }
  .cookies__text {
    width: 100%;
    margin-bottom: 0.875rem;
    font-size: 0.875rem;
  }
  .cookies__btns {
    justify-content: start;
    gap: 1.875rem;
  }
}

@media (max-width: 550px) {
  .cookies__text {
    font-size: 0.75rem;
    margin-bottom: 0.875rem;
    text-align: center;
  }

  .cookies__btns {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.625rem;
  }
}
