.experience {
  position: relative;
  &__wrap {
    position: relative;
  }

  &__bg {
    position: absolute;
    top: -8rem;
    left: -5rem;
    width: 42.5rem;
    height: auto;
    z-index: -1;
  }

  &__title {
    margin-bottom: 3.75rem;
  }

  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__item {
    flex: 0 0 14.5rem;
    text-align: center;
    .experience__item-bg:nth-child(5) {
      background: url("../img/circle-50.svg") no-repeat center;
    }
  }


  &__item-bg {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0.75rem;
    margin: 0 auto;
  }


  &__item-num {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 3.125rem;
    line-height: 100%;
    text-align: center;
    color: var(--2-3);
  }

  &__item-text {
    margin-top: 1.0975rem;
    font-size: 1.25rem;
    text-align: center;
    line-height: 120%;
  }
  &__bg-background {
    position: absolute;
    bottom: 0rem;
    right: 0rem;
    width: 60.9375rem;
    height: auto;
    z-index: -1;
  }
}

.experience__item:nth-child(2) .experience__item-num{
  font-size: 2.5rem;
}
.experience__item:nth-child(3) .experience__item-num span{
  font-size: 2.5rem;
}
.experience__item:nth-child(3) .experience__item-num {
  line-height: 80%;
}
.experience__item:nth-child(4) .experience__item-num span{
  font-size: 2.5rem;
}
.experience__item:nth-child(4) .experience__item-num {
  line-height: 80%;
}

.raz {
  --borderW: 0.625rem;
  position: relative;
  display: inline-grid;
  place-content: center;
  width: 10rem;
  height: 10rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 0.625rem solid rgba(165, 205, 185, 0.1);
}
.raz::before {
  content: "";
  position: absolute;
  inset: calc(-1*var(--borderW));
  /* border: var(--borderW) dotted darkturquoise; */
  border: 0.625rem solid var(--2-1);
  border-radius: 50%;
  animation: progress 3s cubic-bezier(.25, .4, .75, .6) forwards;
}

@keyframes progress {
    0% { clip-path: polygon(50% 50%, 50% -21%, 50% -21%, 50% -21%, 50% -21%, 50% -21%); }
   25% { clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 121% 50%, 121% 50%, 121% 50%); }
   50% { clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, 50% 121%, 50% 121%); }
   75% { clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, -21% 50%, -21% 50%); }
  100% { clip-path: polygon(50% 50%, 50% -21%, 121% 50%, 50% 121%, -21% 50%, 50% -21%); }
}


@media (max-width: 768px) {
  .experience__item-bg {
    width: 6.875rem;
    height: 6.875rem;
  }
  .experience__title {
    margin-bottom: 2.5rem;
  }
  .experience__item {
    flex: 0 0 8.4375rem;
}
.experience__item-num {
  font-size: 2.5rem;
}
.experience__item:nth-child(2) .experience__item-num {
  font-size: 1.75rem;
}
.experience__item:nth-child(3) .experience__item-num {
  line-height: 70%;
}
.experience__item:nth-child(3) .experience__item-num span {
  font-size: 1.75rem;
}
.experience__item-text {
  font-size: 0.875rem;
  margin-top: 0.32375rem;
}
.experience__item:nth-child(4) .experience__item-num span {
  font-size: 1.75rem;
}
.experience__item:nth-child(5) .experience__item-bg {
  width: 6.875rem;
    height: 6.875rem;
    margin-bottom: 0.375rem;
}
.experience__bg {
  top: -4rem;
  left: -1rem;
  width: 28.5rem;
  height: auto;
}
.experience__bg-background {
  width: 29rem;
}

}

@media (max-width: 550px) {
  .experience__list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.9rem 0.875rem;
  }
  .experience__item-bg {
    width: 6.25rem;
    height: 6.25rem;
    margin-bottom: 0.125rem;
}
.experience__title {
  margin-bottom: 1.25rem;
}
.experience__item-num {
  font-size: 2rem
}
.experience__item:nth-child(2) .experience__item-num {
  font-size: 1.375rem;
}
.experience__item:nth-child(3) .experience__item-num span {
  font-size: 1.375rem;
line-height: 80%;
}
.experience__item:nth-child(4) .experience__item-num span {
  font-size: 1.375rem;
line-height: 80%;
}
.experience__item-text {
  font-weight: 300;
font-size: 0.75rem;
}
.experience__item:nth-child(5) .experience__item-bg {
  width: 6.25rem;
    height: 6.25rem;
    margin-bottom: 0.125rem;
}

.experience__bg {
  width: 15.9375rem;
  top: -3rem;
}
.experience__bg-background {
  width: 24.0625rem;
}
.experience__item {
  flex: 0 0 8.84375rem;
}

