.payments {
    position: relative;
    padding-bottom: 0rem;

    &__flowers {
      position: absolute;
      z-index: -1;
      top: 1.5rem;
      left: -3.6rem;
      width: 40.1875rem;
      height: 12rem;
    }
    &__bg {
      position: absolute;
      z-index: -1;
      bottom: 0rem;
      right: -10rem;
      width: 58.75rem;
      height: 34.0625rem;
    }

    &__container {
      max-width: 66.25rem;
      margin: 0 auto;
    }

		&__text {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 1.25rem;
		}

		&__list-title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 2rem;
      line-height: 120%;
      color: var(--2-dark);
      margin: 2.5rem 0 1.5rem;

		}
    &__list-title--back {
      margin-top: 1.25rem;
      margin-bottom: 1.2rem;
    }

		&__list-subtitle {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 1.25rem;
      line-height: 120%;
      color: var(--2-dark);
      margin-bottom: 0.875rem;
      margin-left: 0.4rem;
		}

    &__list-subtitle--online {
      margin-bottom: 1rem;
    margin-top: 2.5rem;
    }

		&__tall {
      // width: 61.375rem;
      margin-left: 4.8rem;
		}

		&__tall-text {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.25rem;
      line-height: 130%;
      color: var(--2-dark);
      margin-bottom: 1.25rem;
      border-radius: 0 0.625rem 0.625rem 0;
      border-left: 0.125rem solid var(--2-1);
      background: #edf5f1;
      padding: 0.9375rem 0.75rem;
      span {
        display: block;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 1.5rem;
        line-height: 130%;
        color: var(--2-dark);
        margin-bottom: 0.5rem;
      }
		}

    &__text--last {
      margin-bottom: 0;
    }
}

@media (max-width: 768px) {
  .price {
    padding-bottom: 3.2rem;
}
  .payments__text {
    font-size: 0.875rem;
    margin-bottom: 0.525rem;
    letter-spacing: 0.07px;
  }
  .payments__list-title {
    font-size: 1.25rem;
    margin: 1.25rem 0 1.0rem;
  }
  .payments__list-subtitle {
    font-size: 1rem;
    margin-bottom: 0.5875rem;
  }
  .payments__tall-text {
    font-size: 0.875rem;
    padding: 0.59375rem 0.475rem;
    margin-bottom: 0.625rem;
  }
  .payments__tall-text:last-child{
    margin-bottom: 0;
  }
  .payments__tall-text span {
    font-size: 1.25rem;
    line-height: 100%;
  }
  .payments__tall {
    margin-left: 1.18rem;
}
.payments__list-title--back {
  margin-bottom: 0.26rem;
  margin-top: 1.31rem;
}
.payments__list-subtitle--online {
  margin-top: 1.25rem;
  margin-bottom: 0.625rem;
}
.payments__list-subtitle--card {
  margin-top: 1.25rem;
  margin-bottom: 0.8125rem;
}
.payments__flowers {
width: 22.375rem;
height: auto;
top: 1.5rem;
    left: -2.6rem;
}
.payments__bg {
  width: 21.25rem;
  height: auto;
  right: 0rem;
}
}

@media (max-width: 550px) {
  .payments__text {
    font-size: 0.75rem ;
  }
  .payments__list-title {
    font-size: 1rem;
  }
  .payments__list-subtitle {
    font-size: 0.875rem;
  }
  .payments__tall-text {
    font-size: 0.75rem;
  }
  .payments__tall-text span {
    font-size: 0.875rem;
}
.payments__tall {
  margin-left: 1.18rem;
  margin-top: 1.2rem;
  margin-bottom: 1.5rem;
}
.payments__text {
  margin-bottom: 0.3525rem;
}
.price {
  padding-bottom: 1.72rem;
}
.payments__list-subtitle--online {
  margin-top: 0.25rem;
  margin-bottom: 0.625rem;
}
}

