.reviews-main {
  position: relative;
  padding-bottom: 4.4rem;
  &__bg {
    position: absolute;
    z-index: -1;
    bottom: 0rem;
    left: 0rem;
  }
  &__wrap {
    display: flex;
    // gap: 1.875rem;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 1rem 1rem;
  }

  &__link {
    // flex: 0 0 31.95%;
    // height: 31rem;
    flex: 0 0 32%;
    height: 31.5rem;
    img {
      object-fit: contain;
      // object-fit: cover;
    }
  }

}
@media (max-width: 768px) {
.reviews-main__link {
  height: 22.2rem;
  flex: 0 0 42.8%;
}
.reviews-main__wrap {
  justify-content: center;
  gap: 0.4875rem;
}
.reviews-main {
  padding-bottom: 3.64rem;
}

}

@media (max-width: 550px) {
  .reviews-main__link {
    flex: 0 0 100%;
    height: 20.832rem;
}
.reviews-main__wrap {
  gap: 0.54875rem;
}
.reviews-main {
  padding-bottom: 2.464rem;
}
}



